import React, { Component } from 'react';
import moment from 'moment';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSuitcase, faClock, faExclamationTriangle, faArrowRight, faPlane, faPlay } from '@fortawesome/free-solid-svg-icons';

class FlightInfoSelectedDetails extends Component {
    
    render() {
        const { t } = this.props;
        let flight = this.props.flight;
        let num = this.props.num;
        return (
        <Col xs={12} className="margin-b20 fade-it-in-1 flight-info-open">
            <Row>
                {flight.dep_airport.map((item,i)=>(
                    <Col xs={12} key={i}>
                        <Row>
                            {flight.stop_duration[i] && parseInt(flight.stop_duration[i])>0 &&
                            <Col xs={12} className="">
                                <Col xs={12} >
                                    <Col xs={12} sm={9} className="layover">
                                        <Row>
                                            <Col xs={12} className="text-left padding-lr15">
                                                {flight.arr_airport[i-1] && flight.dep_airport[i]!==flight.arr_airport[i-1] ? 
                                                    <span>
                                                        <FontAwesomeIcon icon={faExclamationTriangle} className="font18 color-red" />
                                                        <span className="font-weight-bold margin-5">
                                                            {t('CHANGE_OF_AIRPORT')}:
                                                        </span>
                                                    </span>:
                                                    <b>
                                                    <span className="margin-r5">
                                                        {t('AIRPORT_STOP')}:
                                                    </span>
                                                </b>
                                                }
                                                {flight.arr_airport[i-1] && flight.dep_airport[i]!==flight.arr_airport[i-1] ?
                                                    <b><span>
                                                        <span>{flight.arr_airport[i-1]}</span>{' '}
                                                        <span><FontAwesomeIcon icon={faArrowRight} /></span>{' '}
                                                        <span>{flight.dep_airport[i]}</span>
                                                        <div className="text-danger font12 maxw400 margin-b10 font-weight-normal margin-t4">{t('DIFF_ARR_DEP_AIRPORT')}</div>
                                                    </span></b>:
                                                    <b><span>
                                                        {flight.dep_airport[i]}
                                                    </span></b>
                                                }
                                            </Col>
                                            <Col xs={12} className="text-left text-gray">
                                                <FontAwesomeIcon icon={faClock} /> 
                                                {' ' + flight.stop_duration[i].slice(0,2)+t("DURATION_H")+" "+flight.stop_duration[i].slice(2,4)+t("DURATION_M")}
                                            </Col>
                                        </Row>
                                    </Col>
                                </Col>
                            </Col>}
                            <Col xs={12} sm={3} md={3} xl={{offset: 1, span: 2}} style={{height: 0}}>
                                <Row className="xs-relative">
                                    
                                    <Col xs sm={12} className="airline">
                                        {this.props.airlines.hasOwnProperty(flight.airline_code[i]) &&
                                            this.props.airlines[flight.airline_code[i]]}
                                        <br />
                                        <FontAwesomeIcon icon={faPlay}  />
                                        <span>
                                            {flight.airline_code[i]}-{flight.flight_num[i]}
                                        </span>
                                    </Col>
                                    <Col  sm={12} className="logos">
                                        <Image 
                                        src={'/images/airlogos/'+flight.airline_code[i].toLowerCase()+'.gif'}
                                        onError={e => {e.target.src = '/images/airlogos/_noimg.gif'}}
                                        className="float-left margin-b10 margin-lr-2"
                                    />
                                    </Col>
                                </Row>
                            </Col>
                            {/* <Col xs={12} sm={3} lg={3} xl={{offset: 1, span: 2}}>
                                <Row className="">
                                    <Col xs={2} sm={12}>
                                        <Image 
                                        src={'/images/airlogos/'+flight.airline_code[i].toLowerCase()+'.gif'}
                                        onError={e => {e.target.src = '/images/airlogos/_noimg.gif'}}
                                        className="float-left margin-b10 margin-lr-2"
                                    />
                                    </Col>
                                    <Col xs sm={12} className="text-left margin-t20">
                                        {flight.airline_name[i]}<br/>
                                        <span className="">
                                            {flight.airline_code[i]} - {flight.flight_num[i]}
                                        </span>
                                    </Col>
                                </Row>
                            </Col> */}
                            <Col xs={12} sm={9} xl={{pull: 1, span: 8}} className="section">
                                <Row>
                                    {/* <Col xs={12} className="text-gray margin-b10">
                                        <div className="float-left"><FontAwesomeIcon icon={faCalendarAlt} />{' '}{moment(flight.dep_flight_date[i],'DDMMM').format('MMM D')}</div>
                                        <div className="float-right"><FontAwesomeIcon icon={faClock} />{' '}{flight.elapsed_flight_time[i].slice(0,2)+"h "+flight.elapsed_flight_time[i].slice(2,4)+"min"}</div>
                                        <div className="clearfix" />
                                        <hr className="border-dotted margin0"/>
                                    </Col> */}
                                    <Col xs={6} className="margin-b5">
                                        <span >
                                            {moment(flight.dep_flight_date[i], 'DMMM').format("MMM D")},{" "}
                                            {flight.dep_flight_time[i].slice(0,2)+":"+flight.dep_flight_time[i].slice(2,4)}
                                        </span>
                                    </Col>
                                    <Col xs={6} className="text-right">
                                        <span className="">
                                            {flight.arr_flight_time[i].slice(0,2)+":"+flight.arr_flight_time[i].slice(2,4)}{' '}
                                        </span>
                                    </Col>
                                    <Col xs={3} className="padding-r0 font-weight-bold">
                                        <span className={flight.arr_airport[i-1] && flight.dep_airport[i]!==flight.arr_airport[i-1]?"text-danger":""}>
                                            {flight.dep_airport[i]}
                                        </span>
                                    </Col>
                                    <Col
                                        xs={6}
                                        className="text-center font12 padding-lr8 text-light-gray">
                                        <FontAwesomeIcon
                                            icon={faPlane}
                                            className="font20 padding-lr2 text-blue move-plane"
                                        />
                                    </Col>
                                    <Col xs={3} className="text-right padding-lr0 font-weight-bold">
                                        <span className={flight.dep_airport[i+1] && flight.arr_airport[i]!==flight.dep_airport[i+1]?"text-danger margin-r10":"margin-r10"}>
                                            {flight.arr_airport[i]}
                                        </span>
                                    </Col>
                                    <Col xs={6} className="font12">
                                        {flight.dep_town[i]}
                                    </Col>
                                    <Col xs={6} className="font12 text-right">
                                        {flight.arr_town[i]}
                                    </Col>
                                    <Col xs={12}>
                                        <div className="text-light-gray margin-t10">
                                            <span className="margin-r10">
                                                <FontAwesomeIcon icon={faClock} />
                                            </span>
                                            {flight.elapsed_flight_time[i].slice(0, 2) +
                                            t('DURATION_H') + ' ' +
                                            flight.elapsed_flight_time[i].slice(2, 4) +
                                            t('DURATION_M')}
                                        </div>
                                    </Col>
                                    {this.props.selectedFlight.baggages_info[num] && 
                                    <Col xs={12} className="padding0">
                                        <Row className="padding-lr10">
                                            <Col xs={1} className="text-light-gray">
                                                <FontAwesomeIcon icon={faSuitcase} />
                                            </Col>
                                            {/* <Col xs={11} className="text-light-gray">
                                                {this.props.selectedFlight.baggages_info[num][i]!=="0PC"?
                                                    this.props.selectedFlight.baggages_info[num][i]:
                                                    "no checked-in baggage"}
                                                {this.props.selectedFlight.baggages_info[num][i].MaxWeight && 
                                                    <span>
                                                        {', '}
                                                        {this.props.selectedFlight.baggages_info[num][i].MaxWeight}
                                                    </span> 
                                                }
                                            </Col> */}
                                        </Row>
                                    </Col>}
                                </Row>
                            </Col>
                        </Row>
                    </Col>
                ))}
            </Row>
        </Col>
        )
    }
}

export default FlightInfoSelectedDetails;


