/* eslint-disable array-callback-return */
import React, { Component } from 'react';
import Form from 'react-bootstrap/Form';
import Dropdown from 'react-bootstrap/Dropdown';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import Modal from 'react-bootstrap/Modal';
import moment from 'moment';
import 'moment/min/locales.min';
import currencies from '../common/currencies';
import langs from '../common/langs';

// control languages and currencies
class LangCurr extends Component {
  constructor(props) {
    super(props);
    this.state = {
        showModal: false
    };
    this.changeLang = false;
    this.changeCurr = false;
  }

  countrySelector = (pickCountry, displayLang, langs) => {
    return(
      <div>
        <div className="text-left margin-t20 margin-b10">{pickCountry}</div>
        <Dropdown>
          <Dropdown.Toggle id="pickCountry" className="width-100 overflow-elipsis" variant="outline-secondary">
              <span className="padding-r20">
                <Image
                  src={'/images/flags/' + displayLang.value + '.gif'}
                  className="padding-r20"
                />
              <span>{displayLang.value}{' - '}{displayLang.name}</span>
            </span>
          </Dropdown.Toggle>
          <Dropdown.Menu className="height-75vh width-100 overflow-auto">
            {langs.slice().reverse().map((item, i) => (
              item.top==="1" && 
              <Dropdown.Item
                className={displayLang.value===item.value?"font14 overflow-elipsis margin-t4 padding-tb5 text-blue back-light-blue":"font14 overflow-elipsis margin-t4 padding-tb5 text-gray"}
                onClick={() => this.selectLang(item.value)}
                key={i}
              >
                <div>
                  <Image
                    src={'/images/flags/' + item.value + '.gif'}
                    className="padding-r20"
                  />
                  <span>
                    {item.value}
                    {' - '}
                    {item.name}
                  </span>
                </div>
              </Dropdown.Item>
            ))}
            <Dropdown.Item className="font14" disabled>- - -</Dropdown.Item>
            {langs.map((item, i) => (
              <Dropdown.Item 
                className={displayLang.value===item.value?"font14 overflow-elipsis margin-t4 padding-tb5 text-blue back-light-blue":"font14 overflow-elipsis margin-t4 padding-tb5 text-gray"}
                onClick={() => this.selectLang(item.value)}
                key={i}
              >
                <Image
                  src={'/images/flags/' + item.value + '.gif'}
                  className="padding-r20"
                />
                <span>
                {item.value}
                {' - '}
                {item.name}
                </span>
              </Dropdown.Item>
            ))}
          </Dropdown.Menu>
        </Dropdown>
      </div>
    )
  }

  currencySelector = (pickCurrency, displayCurr, currencies) => {
    return(
      <div>
        <div className="text-left margin-t20 margin-b10">{pickCurrency}</div>
        <Dropdown>
          <Dropdown.Toggle id="pickCountry" className="width-100 overflow-elipsis" variant="outline-secondary">
            <span className="padding-r20">{displayCurr.cc}{' - '}{displayCurr.symbol}{' ('+displayCurr.name+')'}</span>
          </Dropdown.Toggle>
          <Dropdown.Menu className="height-60vh width-100 overflow-auto">
            {currencies.slice().reverse().map((item, i) => (
              item.top==="1" && 
              <Dropdown.Item
                className={displayCurr.cc===item.cc?"font14 overflow-elipsis margin-t4 padding-tb5 text-blue back-light-blue":"font14 overflow-elipsis margin-t4 padding-tb5 text-gray"}
                onClick={() => this.selectCurrency(item.cc)}
                key={i}
              >
                <span>
                  <span className="font-weight-bold">{item.cc}</span>
                  {' - '}
                  {item.symbol}
                  {' ('+item.name+')'}
                </span>
              </Dropdown.Item>
            ))}
            <Dropdown.Item className="font14" disabled>- - -</Dropdown.Item>
            {currencies.map((item, i) => (
              <Dropdown.Item
                className={displayCurr.cc===item.cc?"font14 overflow-elipsis margin-t4 padding-tb5 text-blue back-light-blue":"font14 overflow-elipsis margin-t4 padding-tb5 text-gray"}
                onClick={() => this.selectCurrency(item.cc)}
                key={i}
              >
                <span>
                  <span className="font-weight-bold">{item.cc}</span>
                  {' - '}
                  {item.symbol}
                  {' ('+item.name+')'}
                </span>
              </Dropdown.Item>
            ))}
          </Dropdown.Menu>
        </Dropdown>
      </div>
    )
  }

  setLanguage = (language, select=false) => {
    let selected = {};
    langs.filter(function(obj) {
      if (obj.value === language) {
        selected = obj;
        selected['url'] = selected.value;
      }
    });
    this.props.appActions.changeLang(selected);
    if(select && !this.changeCurr) this.setCurrency(selected.currency);
  }

  selectLang = (value) => {
    // e.preventDefault();
    console.log(value)
    this.setLanguage(value, true);
    this.props.appActions.setRouteInfo(this.setRoute(window.location.pathname));
    this.changeLang = true;
  }
  
  setCurrency = (currency) => {
    let selected = {};
    currencies.filter(function(obj) {
      if (obj.cc === currency) {
        selected = obj;
      }
    });
    let currencySelected = {
      cc: selected.cc,
      name: selected.name,
      symbol: selected.symbol,
      url: selected.cc
    };
    this.props.appActions.changeCurrency(currencySelected);
  }

  selectCurrency = (value) => {
    // e.preventDefault();
    this.setCurrency(value);
    this.props.appActions.setRouteInfo(this.setRoute(window.location.pathname));
    this.changeCurr = true;
  }

  setRoute = (loc) => {
    let route = null;
    if (loc.includes('/search/')) {
      route = 'search';
    } else if (loc.includes('/book/')) {
      route = 'book';
    } else if (loc.includes('/info/')) {
      route = 'info';
    } else {
      route = 'home';
    }
    return route;
  }

  toggleModal = (modal) => {
    if(this.changeCurr || this.changeLang) {
      window.location.assign(window.location.pathname);
    } else {
      this.changeCurr = false;
      this.changeLang = false;
      this.setState({ showModal: !modal });
    }
  }

  componentWillMount() {
    const { i18n } = this.props;
    // if empty
    // if(this.props.curr && this.props.curr==={}) {
    //   this.setCurrency('USD');
    // }
    // if(this.props.lang && this.props.lang==={}) {
    //   i18n.changeLanguage('en_US');
    //   this.setLanguage('en');
    // }
    // if the currency url changes
    if (this.props.curr && this.props.curr.url !== this.props.curr.cc) {
      let currUrl = this.props.curr.url;
      this.setCurrency(currUrl);
    }
    let langParam = this.props.match.params.lang?
      this.props.match.params.lang:
      'en';
    let x = this.props.lang;
    langs.filter(function(obj) {
      if (obj.value === langParam) {
        x = obj;
        x['url'] = obj.value;
      }
    });
    if(x!==null) {
      i18n.changeLanguage(x.value);
      this.props.appActions.changeLang(x);
    }
  } 

  render() {
    const { t } = this.props;
    let valLang = '';
    let valCurr = '';
    if (typeof this.props.lang !== 'undefined' && this.props.lang !== null) {
      valLang = (
        <div>
          <div className="text-blue font-weight-bold">{t('PICK_COUNTRY')}</div>
          <div className="clear-all" />
        </div>
      );
      if (this.props.lang && moment.locale() !== this.props.lang.locale) {
        moment.locale(this.props.lang.locale);
      }
      if (this.props.curr) {
        valCurr = (
          <div>
            <div className="text-blue font-weight-bold">{t('PICK_CURRENCY')}</div>
            <div className="clear-all" />
          </div>
        );
      }
      return (
        <div>
          {this.props.curr && this.props.lang && (
            <div className={this.props.isOpen ? "d-block":"d-none" }>
              <Button
                  variant="outline"
                className="margin-lr-0 width-100 text-light-gray"
                onClick={() => this.toggleModal(this.state.showModal)}
              >
                <Image
                  src={'/images/flags/' + this.props.lang.value + '.gif'}
                  className="flagMargin"
                />{' '}
                {this.props.curr.cc}
                {' - '}
                {this.props.curr.symbol}
              </Button>
            </div>
          )}
          <Modal
            className="modalMsg2"
            show={this.state.showModal}
            onHide={() => this.toggleModal(this.state.showModal)}
          >
            <Modal.Body className="text-center">
              <Col xs={12}>
                <Form>
                  <Form.Group as={()=>this.countrySelector(valLang, this.props.lang, langs)} controlId="formControlsSelectCountry">
                    {/* <Form.Label className="float-left font18">
                      <div className="text-left">{valLang}</div>
                    </Form.Label>
                    <Form.Control
                      as="select"
                      onChange={e => this.selectLang(e)}
                      value={this.props.lang.value}
                      className="input-sm"
                    >
                      {langs.slice().reverse().map((item, i) => (
                        item.top==="1" && 
                        <option 
                          className="font14"
                          value={item.value} 
                          key={i}
                        >
                          <div>
                          {item.value}
                          {' - '}
                          {item.name}
                          </div>
                        </option>
                      ))}
                      <option className="font14" disabled>- - -</option>
                      {langs.map((item, i) => (
                        <option 
                          className="font14"
                          value={item.value} 
                          key={i}
                        >
                          {item.value}
                          {' - '}
                          {item.name}
                        </option>
                      ))}
                    </Form.Control> */}
                  </Form.Group>
                  <Form.Group as={()=>this.currencySelector(valCurr, this.props.curr, currencies)} controlId="formControlsSelectCurrency">
                    {/* <Form.Label className="float-left font18">
                      <div className="text-left">{valCurr}</div>
                    </Form.Label>
                    <Form.Control
                      as="select"
                      onChange={e => this.selectCurrency(e)}
                      value={this.props.curr.cc}
                      className="input-sm"
                    >
                      {currencies.slice().reverse().map((item, i) => (
                        item.top==="1" &&
                        <option
                          className="font14"
                          value={item.cc}
                          key={i}
                        >
                          {item.cc}
                          {' - '}
                          {item.symbol}
                          {' ('+item.name+')'}
                        </option>
                      ))}
                      <option className="font14" disabled>- - -</option>
                      {currencies.map((item, i) => (
                        <option
                          className="font14"
                          value={item.cc}
                          key={i}
                        >
                          {item.cc}
                          {' - '}
                          {item.symbol}
                          {' ('+item.name+')'}
                        </option>
                      ))}
                    </Form.Control> */}
                  </Form.Group>
                  <Col xs={12} className="margin-t50 padding0">
                    <Button
                        className="width-100"
                        onClick={() => this.toggleModal(this.state.showModal)}
                    >
                        {t('BUTTON_CLOSE')}
                    </Button>
                    </Col>
                    <div className="clear-both" />
                </Form>
                </Col>              
            </Modal.Body>
          </Modal>
        </div>
      );
    } else return null;
  }
}
export default LangCurr;
