import initialStateFlightAdd from './initialStateFlightAdd';
import {
    SET_BAG_INFO,
    SET_FLIGHT_KEY
} from '../actions/allActions';

export default function flightAddRed(state = initialStateFlightAdd, action) {
    switch (action.type) {
        case SET_BAG_INFO:
            return { ...state, bagInfo: action.bagInfo };
        case SET_FLIGHT_KEY:
                return { ...state, flightKey: action.flightKey };
        default:
            return state;
        }

}