import initialStateForm from './initialStateForm';
import {
    SET_PEOPLE_INFO,
    SET_VALIDATION,
    SET_ADDITIONAL_SERVICES,
} from '../actions/allActions';

export default function formRed(state = initialStateForm, action) {
    switch (action.type) {
        case SET_PEOPLE_INFO:
            return { ...state, peopleInfo: action.peopleInfo };
        case SET_VALIDATION:
            return { ...state, validation: action.validation };
        case SET_ADDITIONAL_SERVICES:
            return { ...state, additionalServices: action.additionalServices };
        default:
            return state;
        }

}

