import React, { Component } from 'react';
import moment from 'moment';
import FlightInfoSelectedDetails from './FlightInfoSelectedDetails';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import Modal from 'react-bootstrap/Modal';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSuitcase, faPlane, faClock, faHandPaper, faCalendarAlt, faInfoCircle } from '@fortawesome/free-solid-svg-icons';
import { faPlusSquare, faMinusSquare } from '@fortawesome/free-regular-svg-icons';
import Luggage from './Luggage';
import FlightVertical from './FlightVertical';

class FlightInfoSelected extends Component {
    constructor(props) {
      super(props);
      this.state={
          addInfo: {},
          modalBag: false
      }
    }

    setAddInfo = (num) => {
        let addInfoTemp = this.state.addInfo;
        if(addInfoTemp.hasOwnProperty(num.toString())) delete addInfoTemp[num.toString()]
        else addInfoTemp[num.toString()] = true;
        this.setState({
            addInfo: addInfoTemp
        })
    }

    renderMoreInfo = (open) => {
        const { t } = this.props;
        return(
            <div className="flight-info-detail exception">
                <FontAwesomeIcon 
                    icon={open?faMinusSquare:faPlusSquare} 
                    className="cursor-pointer font16"
                />{" "}
                <span className="margin-r5">{t('DETAILS').toLowerCase()}</span>
            </div>
        )
    }
    
    renderFlight = (num) => {
        const { t } = this.props;
        let tempArrCode = [];
        return (
            <Row className="search-container margin-b10">
                {/* {(num!==0) && <Col xs={12}><hr className="hr1"/></Col>} */}
                <Col xs={4} sm={4} md={4} lg={4} className="exception">
                    <Row className="margin-t30">
                        <Col xs={11} className="top-logos padding-r-logos">
                            {this.props.selectedFlight.airline_code[num].map((item,i) => (
                                <div key={i}>
                                    {!tempArrCode.includes(item) && tempArrCode.push(item) &&
                                        <Image 
                                            key={i}
                                            src={typeof item==="string"?'/images/airlogos/'+item.toLowerCase()+'.gif':'/images/airlogos/_noimg.gif'}
                                            onError={e => {e.target.src = '/images/airlogos/_noimg.gif'}}
                                            className="float-left margin-b5 margin-lr-2"
                                        />
                                    }
                                </div>
                            ))}
                        </Col>
                    </Row>
                    {/* <Col className="more-info-holder text-blue cursor-pointer d-none d-sm-block padding-lr0" onClick={()=>this.setAddInfo(num)}>
                        {this.renderMoreInfo(this.state.addInfo[num.toString()])}
                    </Col> */}
                </Col>
                <Col xs={8} sm={8} className="padding-r0 padding-l-logos">
                    <Col xs={12} className="text-light-gray padding-lr0 margin-b5 font14">
                        <FontAwesomeIcon icon={faCalendarAlt} className="margin-r10"/>
                        {moment(this.props.selectedFlight.dep_flight_date[num],'DDMMM').format('LL')}
                    </Col>
                    <Row className="padding-lr10">
                        <Col xs={5} md={4} className="padding0">
                            <div>
                                <span className="margin-r10 font16">
                                    <b>{this.props.selectedFlight.flight_info[num].dep_airport[0]}</b>
                                </span>
                                <span className="font14  max-content">
                                    {this.props.selectedFlight.dep_flight_time[num][0].slice(0,2)+":"+this.props.selectedFlight.dep_flight_time[num][0].slice(2,4)}
                                </span>
                            </div>
                            {/* <div className="font12 font-weight-light overflow-elipsis">{this.props.selectedFlight.flight_info[num].dep_town[0]}</div> */}
                        </Col>
                        <Col xs={2} className="font16 cursor-pointer padding0 exception" onClick={()=>this.setAddInfo(num)}>
                            <FontAwesomeIcon icon={faPlane} className="svg"/>
                        </Col>
                        <Col xs={5}  className="padding0">
                            <div>
                                <span className="font16 margin-r10 ">
                                    <b>{this.props.selectedFlight.flight_info[num].arr_airport[this.props.selectedFlight.arr_flight_time[num].length-1]}</b>
                                </span>
                                <span className="font14  max-content">
                                    {this.props.selectedFlight.arr_flight_time[num][this.props.selectedFlight.arr_flight_time[num].length-1].slice(0,2)+":"+this.props.selectedFlight.arr_flight_time[num][this.props.selectedFlight.arr_flight_time[num].length-1].slice(2,4)}
                                </span>
                                <span>
                                    {moment(this.props.selectedFlight.arr_flight_date[num][this.props.selectedFlight.arr_flight_date[num].length - 1], "DMMM").diff(
                                        moment(this.props.selectedFlight.arr_flight_date[num][0], "DMMM"),
                                        "days"
                                    ) > 0 && (
                                        <span
                                            className="next-day"
                                            onClick={e =>
                                                e.currentTarget.querySelector("div").classList.toggle("show")
                                            }
                                            onMouseOver={e =>
                                                e.currentTarget.querySelector("div").classList.toggle("show")
                                            }
                                            onMouseOut={e => {
                                                if (e.currentTarget.querySelector("div").classList.length) {
                                                    e.currentTarget.querySelector("div").classList.toggle("show")
                                                }
                                            }}>
                                            <div>
                                                <span>
                                                    Arriving{" "}
                                                    {moment(this.props.selectedFlight.arr_flight_date[num][this.props.selectedFlight.arr_flight_date[num].length - 1], "DMMM").diff(
                                                        moment(this.props.selectedFlight.flight_info[num].dep_flight_date[0], "DMMM"),
                                                        "days")}{" "}
                                                    day(s) later.
                                                </span>
                                            </div>
                                            +
                                            {moment(this.props.selectedFlight.arr_flight_date[num][this.props.selectedFlight.arr_flight_date[num].length - 1], "DMMM").diff(
                                                moment(this.props.selectedFlight.dep_flight_date[num][0], "DMMM"),
                                                "days"
                                            )}
                                        </span>
                                    )}
                                </span>
                            </div>
                            {/* <div className="font12 font-weight-light overflow-elipsis">{this.props.selectedFlight.flight_info[num].arr_town[this.props.selectedFlight.flight_info[num].arr_town.length-1]}</div> */}
                        </Col>
                    </Row>
                    <Row className="margin-t10 font14">
                        {/* <Col xs={2} className="text-left text-light-gray">
                            <FontAwesomeIcon icon={faSuitcase} 
                                className="font18 text-blue margin-lr-2 cursor-pointer" 
                                onClick={()=>this.setState({ modalBag: true })}
                            />
                        </Col> */}
                        <Col xs={5} md={4} className="text-light-gray">
                            <FontAwesomeIcon icon={faHandPaper} />{' '}
                            {console.log(this.props.selectedFlight.arr_airport[num].length)}
                            {this.props.selectedFlight.arr_airport[num].length !== 1 ? 
                                `${this.props.selectedFlight.arr_airport[num].length-1} stop(s)`:
                                t("NONSTOP").toLowerCase()}
                        </Col>
                        <Col xs={2}></Col>
                        <Col xs={5} className="text-light-gray padding0">
                            <FontAwesomeIcon icon={faClock} />{' '}
                            {Math.floor(this.props.selectedFlight.total_mins[num]/60)}{t("DURATION_H")+" "}
                            {this.props.selectedFlight.total_mins[num]%60}{t("DURATION_M")}
                        </Col>
                    </Row>
                </Col>
                <Col xs={{span: 12}} sm={{offset: 4, span: 8}}>
                    <Col md={{offset: 6, span: 5}} sm={{offset: 7, span: 5}}
                        className="cursor-pointer d-block z-index-100" onClick={()=>this.setAddInfo(num)}>
                        {this.renderMoreInfo(this.state.addInfo[num.toString()])}
                    </Col>
                </Col>
                {this.state.addInfo[num.toString()] && <FlightInfoSelectedDetails {...this.props} flight={this.props.selectedFlight.flight_info[num]} num={num} /> }
            </Row>
        )
    }

    handleAddLuggage = () => {

    }

    handleSelect = (flight) => {
        //select the flight
        flight['urlResults'] = window.location.href;
        this.props.appActions.setSelectedFlight(flight);
        this.props.appActions.setRouteInfo('book');
    }

    
    componentWillReceiveProps(nextProps) {
        if(this.props.page!==nextProps.page) {
            this.setState({
                addInfo: {}
            })
        }
    }

    componentWillMount() {
        if(this.props.selectedFlight.cache_key+this.props.selectedFlight.book!==this.props.flightKey) {
            this.props.appActions.setFlightKey(this.props.selectedFlight.cache_key+this.props.selectedFlight.book);
            this.props.appActions.setBagInfo({ })
        }
    }
    
    render() {
        const { t } = this.props;
        return(
            <Col xs={12} className="padding-tb10 text-left">
                {this.props.selectedFlight && this.props.selectedFlight.TOTAL_PRICE &&
                <div>
                    <Modal show={this.state.modalBag} onHide={()=>this.setState({ modalBag: false })}>
                        <Modal.Header closeButton>
                            <Modal.Title>Add Luggage</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <Luggage {...this.props} />
                        </Modal.Body>
                    </Modal>
                    <Card>
                        <Card.Body className="padding-lr10 padding-tb15">
                            <Row className="parent">
                                <Col xs={12}>
                                    {this.props.selectedFlight.to_city.map((item,i) => 
                                        <div key={i}>
                                            {this.renderFlight(i)}
                                        </div>
                                    )}
                                </Col>
                            </Row>
                            <Row>
                                <Col xs={12} className="float-right margin-t10 text-right">
                                    <div className="text-light-gray text-lowercase font-12 margin-r20">
                                        <div className="d-block d-lg-none float-right" onClick={()=>this.props.setModalPrice(true)} ><span className="margin-r10"></span><FontAwesomeIcon icon={faInfoCircle} className="fa-2x cursor-pointer" /></div>
                                        <div className="float-right margin-t10">{t('TOTAL_AIRFARE')}</div>
                                    </div>
                                    <div className="clearfix" />
                                    <div className="float-right text-right font28 margin-r20">
                                        <strong>{parseFloat(this.props.selectedFlight.TOTAL_PRICE).toLocaleString(this.props.lang.locale.replace('_','-')) }</strong>{' '}{this.props.selectedFlight.currency}
                                    </div>
                                    <div className="clearfix"/>
                                        {/* <div className="text-light-gray text-right">
                                            {parseInt(this.props.selectedFlight.search.adult) +
                                            parseInt(this.props.selectedFlight.search.child) +
                                            parseInt(this.props.selectedFlight.search.infant) +
                                            parseInt(this.props.selectedFlight.search.senior) +
                                            parseInt(this.props.selectedFlight.search.youth)}{' '}
                                            {t('ALL_TRAVELERS')}
                                        </div> */}
                                </Col>
                            </Row>
                        </Card.Body>
                    </Card>
                </div>}
            </Col>
        );
    }
}

export default FlightInfoSelected;