import React, { Component } from 'react';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';
import Footer from './Footer';
import ContentHome from './ContentHome';
import ContentSearchFlights from './ContentSearchFlights';
import ContentFlight from './ContentFlight';
import ContentInfo from './ContentInfo';
import ContentAdvertising from './ContentAdvertising';
import Page404 from './Page404';
import Loading from './Loading';
import ContentConfirmation from "./ContentConfirmation";

class RenderRoutes extends Component {
    render() {
        return (
            <div>
                <Loading loading={this.props.loading} />
                <BrowserRouter>
                <Switch>
                    <Route
                    exact
                    path="/:lang/search"
                    render={() => <ContentSearchFlights {...this.props} />}
                    />
                    <Route
                    exact
                    path="/book"
                    render={() => <ContentFlight {...this.props} />}
                    />
                    <Route
                    exact
                    path="/:lang/confirmation"
                    render={() => <ContentConfirmation {...this.props} />}
                    />
                    <Route
                    exact
                    path="/confirmation"
                    render={() => <ContentConfirmation {...this.props} />}
                    />
                    <Route
                    exact
                    path="/:lang/info/:type"
                    render={() => <ContentInfo {...this.props} />}
                    />
                    <Route
                    exact
                    path="/:lang/:type.htm" /*changing static pages to old url format*/
                    render={() => <ContentInfo {...this.props} />}
                    />
                    <Route
                    exact
                    path="/advertising"
                    render={() => <ContentAdvertising {...this.props} />}
                    />
                    <Route
                    exact
                    path="/:lang"
                    render={() => <ContentHome {...this.props} />}
                    />
                    <Route
                    exact
                    path="/"
                    render={() => <ContentHome {...this.props} />}
                    />
                    <Route
                    exact
                    path="/:lang/404/"
                    render={() => <Page404 {...this.props} />}
                    />
                    <Route
                    render={() => {
                        return (
                        <Redirect to={'/'+this.props.lang.value+'/404'} />
                        );
                    }}
                    />
                </Switch>
               {/* <Footer {...this.props} />*/}
                </BrowserRouter>
            </div>
        )
    }
}

export default RenderRoutes;