import React, { Component } from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Row from 'react-bootstrap/Row';
import Container from 'react-bootstrap/Container';
import SearchBar from './SearchBar';
import LangCurr from './LangCurr';
import { LinkContainer } from "react-router-bootstrap";
import MainMenu from './MainMenu';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPhoneVolume } from '@fortawesome/free-solid-svg-icons';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
        isOpen: false
    }
  }

menuToggle = (isOpen) => {
    if(this.state.isOpen!==isOpen)
        this.setState({
            isOpen: isOpen
        });
}

render() {
    const { t } = this.props;
    let menuStatus = this.state.isOpen ? 'isopen' : '';
    return (
        <div>
            <div className={menuStatus} id='menu' onClick={()=>this.menuToggle(false)}>
                <div>
                    <MainMenu isOpen={this.state.isOpen} {...this.props} />
                    {this.state.isOpen && <div className="clearfix margin-b10"></div>}
                    {this.state.isOpen && t('MENU_CRUISES_DN')!=='' && <div className="padding-lr15">
                        <div className="float-right d-block d-md-none width-100 bg-white rad-6">
                            <a href={"tel:"+t('MENU_CRUISES_DN')} title={t('MENU_CRUISES_DN')} className="text-decoration-none text-light-gray">
                                <div className="height-50 padding-tb15">
                                    <FontAwesomeIcon icon={faPhoneVolume} className="margin-r10 font20" />
                                    {t('MENU_CRUISES_DN')}
                                </div>
                            </a>
                        </div>
                    </div>}
                    {this.state.isOpen && <div className="clearfix margin-b10"></div>}
                    {this.props.page!=='flight' && <div className="padding-lr15"><div className="float-left d-block d-md-none width-100 bg-white rad-6"><LangCurr {...this.props} isOpen={this.state.isOpen} /></div></div>}
                    {this.state.isOpen && <div className="clearfix margin-b10"></div>}
                </div>
            </div>
            <Navbar collapseOnSelect bg="light" expand="lg" className="padding-tb0">
                <Container className="transition-all">
                    <Navbar.Brand className="margin-lr-0">
                        <LinkContainer to={"/"+this.props.lang.value}>
                            <Nav.Link href="#" className="padding-lr0">
                                <img
                                    src="/images/st_logo.svg"
                                    width="200"
                                    className="d-inline-block align-top"
                                    alt="sky-tours logo"
                                />
                            </Nav.Link>
                        </LinkContainer>
                    </Navbar.Brand>
                    {/* {this.props.page!=='flight' && <Navbar.Toggle aria-controls="basic-navbar-nav" />} */}
                    {/* {this.props.page!=='flight' && 
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="ml-auto">
                            <LangCurr {...this.props} />
                        </Nav>
                    </Navbar.Collapse>} */}
                    <div> 
                        <div id="hambmenu" className={menuStatus} onClick={()=>this.menuToggle(!this.state.isOpen) }><span></span><span></span><span></span><span></span></div>
                        {this.props.page!=='flight' && <div className="float-right margin-t10 d-none d-md-block"><LangCurr {...this.props} isOpen={true}/></div>}
                        {t('MENU_CRUISES_DN')!=='' && <div className="float-right margin-t7 margin-r20 padding-lr15 padding-tb15 d-none d-md-block">
                            <a href={"tel:"+t('MENU_CRUISES_DN')} title={t('MENU_CRUISES_DN')} className="text-decoration-none text-light-gray"><FontAwesomeIcon icon={faPhoneVolume} className="margin-r10 font20" />{t('MENU_CRUISES_DN')}</a>
                        </div>}
                    </div>
                </Container>    
            </Navbar>
            {this.props.page==='home' && 
                <Row className="home-image">
                    <Container className="image-text">
                        <h1 className="text-left text-white font-weight-bolder">Sky-Tours</h1>
                        <h2 className="text-left text-white font-weight-bolder">Multicity Experts!</h2>
                        <p className="text-left text-white">We check our prices daily to ensure we always offer the best value</p>
                        <div className="clearfix"/>
                    </Container>
                </Row>
            }
            {this.props.page==='search' && 
                <Row className="search-image"></Row>
            }
            {this.props.page==='home' && 
                <Container className="bar-holder">
                    <div className="bar-style shadow">
                        <SearchBar {...this.props} />
                    </div>
                </Container>
            }
        </div>
    )
  }
}

export default Header;
