import React, { Component } from 'react';
import Button from 'react-bootstrap/Button';
import orderBy from 'lodash/orderBy';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faThumbsUp, faMoneyBillAlt, faStopwatch } from '@fortawesome/free-solid-svg-icons';

class Sorting extends Component {
    constructor(props) {
      super(props);
      this.sortIt = this.sortIt.bind(this);
    }

    sortIt(argument, asc) {
        let tempFlights = Object.assign(this.props.flights, {});
        let tempFiltered = this.props.filteredFlights.slice();
        if(argument==='best') {
            tempFlights.alt_inf = orderBy(tempFlights.alt_inf, function (obj) {return parseFloat(obj['total_price'])*obj['total_minutes']}, [asc]);
            tempFiltered = orderBy(tempFiltered, function (obj) {return parseFloat(obj['total_price'])*obj['total_minutes']}, [asc]);
        } else {
            tempFlights.alt_inf = orderBy(tempFlights.alt_inf, function (obj) {return parseFloat(obj[argument])}, [asc]);
            tempFiltered = orderBy(tempFiltered, function (obj) {return parseFloat(obj[argument])}, [asc]);
        }
        this.props.appActions.setFlightsData(tempFlights);
        this.props.appActions.setFilteredFlights(tempFiltered);
        this.props.appActions.setPage(1);
    }

    render() {
        return (
            <Row className="padding-tb5 padding-lr8">
                <Col xs={4} className="padding-lr2">
                <Button variant="outline-primary" className="width-100 font14 padding-lr8" onClick={()=>this.sortIt('best','asc')}>
                    <FontAwesomeIcon icon={faThumbsUp} className="font18"/><br/>
                    Best
                </Button>
                </Col>
                <Col xs={4} className="padding-lr2">
                <Button variant="outline-primary" className="width-100 font14 padding-lr8" onClick={()=>this.sortIt('total_price','asc')}>
                    <FontAwesomeIcon icon={faMoneyBillAlt} className="font18"/><br/>
                    Cheapest
                </Button>
                </Col>
                <Col xs={4} className="padding-lr2">
                <Button variant="outline-primary" className="width-100 font14 padding-lr8" onClick={()=>this.sortIt('total_minutes','asc')}>
                    <FontAwesomeIcon icon={faStopwatch} className="font18"/><br/>
                    Shortest
                </Button>
                </Col>
                <div className="clearfix"/>
            </Row>
        )
    }
}

export default Sorting;