import React, { Component } from "react"
import Container from "react-bootstrap/Container"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import Button from "react-bootstrap/Button"
import Image from "react-bootstrap/Image"

class TrustpilotSection extends Component {
	render() {
		return (
			<Container>
				<Row className="align-items-center justify-content-center">
					<Col xs={12} lg={6} className="trust-pilot d-flex flex-grow-0 flex-column maxw400">
						<div className="float-left">
							<Image src="/images/trustpilot/logo.png" fluid className="float-left" alt="Trustpilot logo" />
						</div>
						<p className="font18">
							We check our prices daily to ensure we offer the best value
						</p>
						<div className="buttons-section margin-b20">
								<Button
									variant="primary"
									className="m-2 secondary highlight"
									onClick={() => window.open("https://www.trustpilot.com/review/www.sky-tours.com", "_blank")}>
									VIEW ALL
								</Button>
								<Button
									variant="danger"
									className="m-2"
									onClick={() =>
										window.open("https://www.trustpilot.com/evaluate/www.sky-tours.com", "_blank")
									}>
									ADD A REVIEW
								</Button>
						</div>
					</Col>
					<Col xs={12} lg={6}>
						<Image src="/images/trustpilot/people.png" fluid alt="people in the background" />
					</Col>
				</Row>
			</Container>
		)
	}
}

export default TrustpilotSection