import React, { Component } from 'react';
import Routes from './Routes';
import Header from './Header';
import TrustpilotSection from './HomeSub/TruspilotSection';
import WhySkyTours from './HomeSub/WhySkyTours';
import InfoMsg from './InfoMsg';
import Row from 'react-bootstrap/Row';
import { withRouter } from 'react-router-dom';

class ContentHome extends Component {
    // constructor(props) {
    //   super(props);
    // }

    componentWillMount() {
        if(this.props.loading!=='none')
            this.props.appActions.loading("none");
    }

    render() {
        
        return(
            <div>
                <Routes {...this.props} />
                <Header {...this.props} page='home'/>
                <WhySkyTours />
                <Row className="plane-image"/>
                <h1 className="padding-tb100">
                    <TrustpilotSection />
                </h1>
                <InfoMsg {...this.props} />
            </div>
        );
    }
}

export default withRouter(ContentHome);