export function setAirlines(filtersTemp, airlinesTemp) {
    return dispatch => {
        let airlines = {}
        axios
            .get("/data/airlines.json")
            .catch(err => {})
            .then(response => {
                let temp = []
                for (let i = 0; i < airlinesTemp.length; i++) {
                    temp = response.data.filter(function(e) {
                        return e.code === airlinesTemp[i]
                    })
                    if (temp && temp[0]) airlines[temp[0].code] = temp[0].name
                    else airlines[airlinesTemp[i]] = airlinesTemp[i]
                }
                if (filtersTemp !== "none") {
                    filtersTemp[6].airlines = airlinesTemp.slice()
                    filtersTemp[6].airlinesConst = airlinesTemp.slice()
                    dispatch(setFilters(filtersTemp))
                }
                dispatch({
                    type: allActions.SET_AIRLINES,
                    airlines: airlines
                })
            })
    }
}

export function fetchFlightsData(reqUrl, flightsSearchInfo, fromUrl=false) {
    let tempSearchInf = cloneDeep(flightsSearchInfo)
    let url =
        A_SKYTOURS +
        reqUrl +
        "&lang=" +
        tempSearchInf.lang.value;
    let filtersTemp = DEFAULT_FILTERS
    return function(dispatch) {
        dispatch(loading("main"))
        dispatch(setTimeStamp(moment()))
        axios
            .get(url)
            .catch(err => {})
            .then(response => {
                if (
                    typeof response !== "undefined" &&
                    response !== null &&
                    response.status === 200
                ) {
                    // console.log(response.data)
                    dispatch(loading("none"));
                    if(response.data.error && 
                    response.data.error.message) {
                        if(response.data.error.message==="NOTHING_FOUND") {
                            dispatch(setInfoMsg("no_results"));
                        } else {
                            dispatch(setInfoMsg("no_results"));
                        }
                    } else {
                        // dispatch(setFlightsData(response.data));
                        dispatch(setFlightsSearchInfo(tempSearchInf))
                        if(!fromUrl) dispatch(setRouteInfo("search"))
                        dispatch(setFilteredFlights([]))
                        dispatch(setPage(1))
                        if (response.data.alt_inf && response.data.alt_inf.length > 0) {
                            let airlinesTemp = []
                            let maxPrice = 0
                            //set  filter total time durations
                            let totTimeInMin = 1000000
                            let totTimeInMax = 0
                            let totTimeOutMin = 1000000
                            let totTimeOutMax = 0
                            let minPrice = Math.floor(
                                parseFloat(response.data.alt_inf[0].total_price)
                            )
                            let tempFlights = Object.assign(response.data, {})
                            for (let i = 0; i < response.data.alt_inf.length; i++) {
                                let flights = []
                                for (let property in response.data.alt_inf[i]) {
                                    if (property.includes("flight")) flights.push(property)
                                    if (property === "inbound") flights.push(property)
                                    if (property === "outbound") flights.push(property)
                                }
                                if (flights.includes("flight1")) flights = flights.sort()
                                //set filter min and max prices
                                if (
                                    parseFloat(response.data.alt_inf[i].total_price) >
                                    maxPrice
                                )
                                    maxPrice =
                                        Math.floor(
                                            parseFloat(response.data.alt_inf[i].total_price)
                                        ) + 1
                                if (
                                    parseFloat(response.data.alt_inf[i].total_price) <
                                    minPrice
                                )
                                    minPrice = Math.floor(
                                        parseFloat(response.data.alt_inf[i].total_price)
                                    )

                                let totTime = 0
                                let totalTime = 0
                                for (let j = 0; j < flights.length; j++) {
                                    //set flight total durations
                                    totTime = 0
                                    if (
                                        Array.isArray(
                                            tempFlights.alt_inf[i][flights[j]].fli_inf
                                        )
                                    ) {
                                        for (
                                            let k = 0;
                                            k <
                                            tempFlights.alt_inf[i][flights[j]].fli_inf.length;
                                            k++
                                        ) {
                                            totTime =
                                                totTime +
                                                parseInt(
                                                    tempFlights.alt_inf[i][
                                                        flights[j]
                                                    ].fli_inf[
                                                        k
                                                    ].elapsed_flight_time.substring(0, 2)
                                                ) *
                                                    60 +
                                                parseInt(
                                                    tempFlights.alt_inf[i][
                                                        flights[j]
                                                    ].fli_inf[
                                                        k
                                                    ].elapsed_flight_time.substring(2, 4)
                                                ) +
                                                parseInt(
                                                    tempFlights.alt_inf[i][
                                                        flights[j]
                                                    ].fli_inf[k].ground_time.substring(0, 2)
                                                ) *
                                                    60 +
                                                parseInt(
                                                    tempFlights.alt_inf[i][
                                                        flights[j]
                                                    ].fli_inf[k].ground_time.substring(2, 4)
                                                )
                                            // prepare airlines
                                            if (
                                                !airlinesTemp.includes(
                                                    tempFlights.alt_inf[i][flights[j]]
                                                        .fli_inf[k].airline_code
                                                )
                                            ) {
                                                airlinesTemp.push(
                                                    tempFlights.alt_inf[i][flights[j]]
                                                        .fli_inf[k].airline_code
                                                )
                                            }
                                        }
                                        tempFlights.alt_inf[i][
                                            "total_minutes_" + flights[j]
                                        ] = totTime
                                    } else {
                                        totTime =
                                            parseInt(
                                                tempFlights.alt_inf[i][
                                                    flights[j]
                                                ].fli_inf.elapsed_flight_time.substring(0, 2)
                                            ) *
                                                60 +
                                            parseInt(
                                                tempFlights.alt_inf[i][
                                                    flights[j]
                                                ].fli_inf.elapsed_flight_time.substring(2, 4)
                                            ) +
                                            parseInt(
                                                tempFlights.alt_inf[i][
                                                    flights[j]
                                                ].fli_inf.ground_time.substring(0, 2)
                                            ) *
                                                60 +
                                            parseInt(
                                                tempFlights.alt_inf[i][
                                                    flights[j]
                                                ].fli_inf.ground_time.substring(2, 4)
                                            )
                                        if (
                                            !airlinesTemp.includes(
                                                tempFlights.alt_inf[i][flights[j]].fli_inf
                                                    .airline_code
                                            )
                                        ) {
                                            airlinesTemp.push(
                                                tempFlights.alt_inf[i][flights[j]].fli_inf
                                                    .airline_code
                                            )
                                        }
                                        tempFlights.alt_inf[i][
                                            "total_minutes_" + flights[j]
                                        ] = totTime
                                    }
                                    //set filter min and max flight duration times
                                    if (
                                        flights[j] === "outbound" ||
                                        flights[j] === "flight1"
                                    ) {
                                        if (totTime > totTimeOutMax) totTimeOutMax = totTime
                                        else if (totTime < totTimeOutMin)
                                            totTimeOutMin = totTime
                                    } else if (
                                        flights[j] === "inbound" ||
                                        flights[j] === "flight2"
                                    ) {
                                        if (totTime > totTimeInMax) totTimeInMax = totTime
                                        else if (totTime < totTimeInMin)
                                            totTimeInMin = totTime
                                    }
                                    //set total time
                                    totalTime = totalTime + totTime
                                }

                                tempFlights.alt_inf[i]["total_minutes"] = totalTime
                            }
                            dispatch(setFlightsData(tempFlights))
                            dispatch(setMaxPrice(maxPrice))
                            dispatch(setMinPrice(minPrice))
                            dispatch(setSbModify(false))
                            filtersTemp[2].price = [minPrice, maxPrice]
                            filtersTemp[2].priceSlider = [minPrice, maxPrice]
                            filtersTemp[5].ttomin = totTimeOutMin
                            filtersTemp[5].ttimin = totTimeInMin
                            filtersTemp[5].ttomax = totTimeOutMax
                            filtersTemp[5].ttimax = totTimeInMax
                            filtersTemp[5].totalTimeOut = totTimeOutMax
                            filtersTemp[5].totalTimeIn = totTimeInMax
                            dispatch(setAirlines(filtersTemp, airlinesTemp))
                        }
                    }
                }
            })
    }
}

export function getFlightDetails(reqUrl, fromSearch) {
    console.log(reqUrl)
    let values = queryString.parse(reqUrl)
    let tempUrl = ""
    // delete values.client_agent
    // delete values.log_id
    // delete values.client_ip
    // delete values["https://xml.sky-tours.com/flights.php?method"]
    for (let property in values) {
        if(BOOK_PARAMS.includes(property))
            tempUrl =
                tempUrl + (tempUrl === "" ? "" : "&") + property + "=" + values[property]
    }
    let url = A_SKYTOURS_DETAILS + tempUrl + A_SKYTOURS_MASTER + A_SKYTOURS_OWNER
    let totalMinutes = 0;
    let totalMinutesAll = [];
    return function(dispatch) {
        dispatch(loading("main"))
        axios
            .get(url)
            .catch(err => {})
            .then(response => {
                if (
                    typeof response !== "undefined" &&
                    response !== null &&
                    response.status === 200
                ) {
                    dispatch(loading("none"));
                    console.log(response.data)
                    if(response.data.hasOwnProperty('TOTAL_PRICE')) {
                        let selectedTemp = response.data
                        // selectedTemp.book_query = tempUrl.split("/").join("&&&")
                        selectedTemp.book_query = tempUrl
                        selectedTemp.from_search = fromSearch
                        for(let j=0; j<selectedTemp.to_city.length; j++) {
                            totalMinutes = 0;
                            for(let k=0; k<selectedTemp.flight_info[j].elapsed_flight_time.length; k++) {
                                totalMinutes = totalMinutes + (
                                    parseInt(selectedTemp.flight_info[j].elapsed_flight_time[k].slice(2,4)) +
                                    parseInt(selectedTemp.flight_info[j].stop_duration[k].slice(2,4)) +
                                    (parseInt(selectedTemp.flight_info[j].elapsed_flight_time[k].slice(0,2)) + parseInt(selectedTemp.flight_info[j].stop_duration[k].slice(0,2))) * 60
                                );
                            }
                            totalMinutesAll.push(totalMinutes);
                        }
                        selectedTemp.total_mins = totalMinutesAll;
                        dispatch(setSelectedFlight(selectedTemp));
                        if(fromSearch) dispatch(setRouteInfo("book"));
                    } else {
                        dispatch(setInfoMsg('no_flight'));
                    }
                }
            })
    }
}

