import React from "react"
import Form from "react-bootstrap/Form"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import Container from "react-bootstrap/Container";
import countriesTrans from "../../common/countriesTrans";

function ContactFields(props) {
  const { 
    t,
    inputCaptureProps,
    defaultValueProps,
    getErrorProps,
    setPersonalInputProps,
    validateFieldsProps,
    countryPhoneDataProps,
    bookForm
  } = props

  //onSelecting a area code, show only the area code and not the country name
  const [select, setSelect] = React.useState(true)
  const [emailBorder, setBorderEmail] = React.useState("")
  const [email2typed, setEmail2Typed] = React.useState(false)

  const setBorderColor = (className, err, init = false) => {
    if (init) className = className.replace("correct", "").replace("has-error", "")
    else if (!err) {
      className = className.includes("correct") ? className : className.concat(" correct")
    } else {
      className = className.replace("correct", " has-error")
      className = className.includes("has-error")
        ? className
        : className.concat(" has-error")
    }
    return className
  }
  const removeErr = (e, type) => {
    setPersonalInputProps(type, null, "", true)
    e.target.className = setBorderColor(
      e.target.className,
      getErrorProps(null, type),
      true
    )
  }

  const emailsMatch = () => {
    if (
      bookForm.email.err === "Email is invalid!" ||
      bookForm.repeatEmail.err === "Email is invalid!" ||
      !email2typed
    ) {
      return
    }

    const equalityCheck = (type1, type2) => {
      const email = new RegExp(`^${bookForm[type1].value}$`, "i")
      if (email.test(bookForm[type2].value)) {
        setPersonalInputProps(type1, undefined, "", true)
        setBorderEmail(pre => {
          return pre
            .replace("has-error", "")
            .replace("correct", "")
            .concat(" correct ")
        })
      } else {
        setPersonalInputProps(type1, undefined, "Both emails must match", true)
        setBorderEmail(pre => {
          return pre
            .replace("has-error", "")
            .replace("correct", "")
            .concat(" has-error ")
        })
      }
    }
    equalityCheck("email", "repeatEmail")
    equalityCheck("repeatEmail", "email")
  }

  return (
    <div className="passengers" style={{ marginTop: 0 }}>
      <h2 className="text-left padding-tb15 Roboto">{t('PLEASE_CONTACT_DETAILS')}</h2>
    <Container className="padding-lr0">
      <Row>
          <Col xs={12} md={5} lg={5} xl={4} className="padding-lr5">
            <Form.Group controlId="formEmail" className="contact-fields">
              <Form.Label className={getErrorProps(null, "email") ? "err-label" : null}>
                {t('EMAIL')}*
              </Form.Label>
              <Form.Control
                type="email"
                className={emailBorder}
                onFocus={e => removeErr(e, "email")}
                onChange={e => {
                  inputCaptureProps("email", e.target.value)
                }}
                onBlur={e => {
                  validateFieldsProps(null, "email")
                  e.target.className = setBorderColor(
                    e.target.className,
                    getErrorProps(null, "email")
                  )
                  emailsMatch()
                }}
                defaultValue={defaultValueProps(null, "email")}
              />
            </Form.Group>
          </Col>
          <Col xs={12} md={5} lg={5} xl={4} className="padding-lr5">
            <Form.Group controlId="formEmailRepeat" className="contact-fields">
              <Form.Label
                className={getErrorProps(null, "repeatEmail") ? "err-label" : null}
              >
                {t('EMAIL_REPEAT')}*
              </Form.Label>
              <Form.Control
                type="email"
                className={emailBorder}
                onPaste={e => e.preventDefault()}
                onFocus={e => {removeErr(e, "repeatEmail"); setEmail2Typed(true)}}
                onChange={e => {
                  inputCaptureProps("repeatEmail", e.target.value)
                }}
                onBlur={e => {
                  validateFieldsProps(null, "repeatEmail")
                  e.target.className = setBorderColor(
                    e.target.className,
                    getErrorProps(null, "email")
                  )
                  emailsMatch()
                }}
                defaultValue={defaultValueProps(null, "repeatEmail")}
              />
            </Form.Group>
          </Col>
        </Row>
        <Row>
        <Col xs={12} md={5} lg={5} xl={4} className="padding-lr5">
          {countryPhoneDataProps && (
            <Form.Group controlId="formPhonePrefix" className="contact-fields">
              <Form.Label
                className={getErrorProps(null, "countryCode") ? "err-label" : null}
              >
                {t('AREA_CODE')}*
              </Form.Label>
              <Form.Control
                as="select"
                onChange={e => {
                  setPersonalInputProps("countryCode", e.target.value)

                  e.target.blur()
                  setSelect(true)
                }}
                onBlur={e => {
                  setPersonalInputProps("countryCode", e.target.value)
                  validateFieldsProps(null, "countryCode")
                  e.target.className = setBorderColor(
                    e.target.className,
                    getErrorProps(null, "countryCode")
                  )
                  setSelect(true)
                }}
                onFocus={e => {
                  removeErr(e, "countryCode")
                  setSelect(false)
                }}
                defaultValue={defaultValueProps(null, "countryCode")}
              >
                <option>- -</option>
                {countryPhoneDataProps.map((item, index) => {
                  return (
                    <option key={index} value={item.dial_code}>
                      {select && item.dial_code===bookForm.countryCode.value ? item.dial_code : `${
                        (countriesTrans.hasOwnProperty(item.name)?
                          t('countries:'+countriesTrans[item.name]):item.name)
                      }: ${item.dial_code}`}
                    </option>
                  )
                })}
              </Form.Control>
            </Form.Group>
          )}
        </Col>
        <Col xs={12} md={5} lg={5} xl={4} className="padding-lr5">
          <Form.Group controlId="formPhoneNum" className="contact-fields">
            <Form.Label
              className={getErrorProps(null, "phoneNumber") ? "err-label" : null}
            >
              {t('DAYTIME_PHONE_NO')}*
            </Form.Label>
            <Form.Control
              type="text"
              onFocus={e => removeErr(e, "phoneNumber")}
              onBlur={e => {
                setPersonalInputProps("phoneNumber", e.target.value)
                validateFieldsProps(null, "phoneNumber")
                e.target.className = setBorderColor(
                  e.target.className,
                  getErrorProps(null, "phoneNumber")
                )
              }}
              defaultValue={defaultValueProps(null, "phoneNumber")}
            />
          </Form.Group>
        </Col>
      </Row>
      </Container>
    </div>
  )
}

export default ContactFields