import React, { Component } from 'react';

class Loading extends Component {

render() {
    return (
        <div>
            {this.props.loading === 'main' &&
                <div className="loading-holder">
                    <div className="loading">
                        <div className="loader1">...loading</div>
                    </div>
                </div>
            }
        </div>
    )
}

}

export default Loading;
