import React, { Component } from 'react';
import FlightInfo from './FlightInfo';
import Sorting from './Sorting';
import Pages from './Pages';
import Col from 'react-bootstrap/Col';
import { Element } from 'react-scroll';
import { PER_PAGE } from '../common/const';
import { paginate } from '../common/utils';

class Flights extends Component {
    render() {
        let flights = [];
        if (
            !this.props.flights ||
            this.props.flights === null ||
            !this.props.flights.alt_inf ||
            this.props.flights.alt_inf.length <= 0
          ) {
            return null;
          } else {
            if (
              typeof this.props.filteredFlights !== 'undefined' &&
              this.props.filteredFlights !== null
            ) {
              this.props.filteredFlights.length > 0
                ? (flights = this.props.filteredFlights)
                : (flights = this.props.flights.alt_inf);
            }
            let pages = Math.floor(flights.length / PER_PAGE);
            let page = 1;
            if (this.props.page <= pages + 1) {
              page = this.props.page;
            } else {
              this.props.appActions.setPage(1);
            }
            flights = paginate(flights, PER_PAGE, page);
        }
        return(
            <div>
                <Element name="pages" />
                <Col xs={12} >
                    <Sorting {...this.props} />
                </Col>
                {
                this.props.flights && this.props.flights.alt_inf && this.props.flights.alt_inf.length>0 &&
                flights.length>0 && flights.map((item,i) => (
                    <Col key={i} className="padding0">
                        <FlightInfo {...this.props} flight={item} index={i} />
                    </Col>
                ))}
                <Col xs={12} className="margin-t50 margin-t20 margin-b50">
                    <Pages {...this.props} />
                </Col>
            </div>
        )
    }
}

export default Flights;