import { Component } from 'react';
import { animateScroll as scroll } from 'react-scroll';

class Routes extends Component {
    // constructor(props) {
    //     super(props);
    // }
    
    setRoute = (route) => {
        if(this.props.routes==='book' &&
        this.props.selectedFlight && 
        (!this.props.selectedFlight.hasOwnProperty('from_search') || 
        !this.props.selectedFlight.from_search)) {
            this.props.history.replace(route);
        } else this.props.history.push(route);
        this.props.appActions.setRouteInfo('none');
        setTimeout(()=>scroll.scrollToTop(),300);
      }
    shouldComponentUpdate(nextProps) {
        if (nextProps.routes !== this.props.routes && nextProps.routes !== 'none' )
            return true;
        else return false;
    }
    componentDidUpdate() {
        if (
            typeof this.props.routes !== 'undefined' &&
            this.props.routes !== null &&
            this.props.history
        ) {
            switch (this.props.routes) {
                case 'home':
                    this.setRoute('/'+this.props.lang.value+'/');
                    break;
                case 'search':
                    this.setRoute('/'+this.props.lang.value+'/search?'+encodeURIComponent(this.props.flightsSearchInfo.url));
                    break;
                case 'book':
                    // this.setRoute('/'+this.props.lang.value+'/book/'+encodeURIComponent(this.props.selectedFlight.book_query));
                    this.setRoute('/book?'+encodeURIComponent(this.props.selectedFlight.book_query));
                    break;
                case 'info':
                    this.setRoute('/'+this.props.lang.value+'/'+this.props.match.params.type+'.htm');
                    break;
                case '404':
                    this.setRoute('/'+this.props.lang.value+'/404');
                    break;
                default:
                    this.setRoute('/');
                    break;
            }
        }
    }

    render() {
        return null;
      }
    }

export default Routes;
