import React, { Component } from "react";
import Card from "react-bootstrap/Card";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
// import moment from 'moment';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faInfoCircle } from '@fortawesome/free-solid-svg-icons';
// import { faPlusSquare, faMinusSquare } from '@fortawesome/free-regular-svg-icons';
import FlightPriceDetails from './FlightPriceDetails';

class FlightVertical extends Component {
	constructor(props) {
		super(props);
		this.state={
			// addInfo: {},
			flightPriceDetails: false
		}
	}

	toggleDetails = (e, i) => {
		e.currentTarget.nextElementSibling.classList.toggle("show");
		let addInfoTemp = this.state.addInfo;
		if(addInfoTemp.hasOwnProperty(i.toString())) delete addInfoTemp[i]
		else addInfoTemp[i] = true;
		this.setState({addInfo: addInfoTemp});
	}

	componentDidMount() {
		if(this.props.modal) {
			this.setState({flightPriceDetails: this.props.modal})
		}
	}

	render() {
		const { t } = this.props;
		let numPeople = 	parseInt(this.props.selectedFlight.search.adult) +
							parseInt(this.props.selectedFlight.search.child) +
							parseInt(this.props.selectedFlight.search.infant) +
							parseInt(this.props.selectedFlight.search.senior) +
							parseInt(this.props.selectedFlight.search.youth);
		return (
			<Card className={this.props.modal?"font14 no-border":"font14 no-border background-blue"} id="v-flight">
				{!this.props.modal && <Row xs={12}>
					<h4>
						{t('SUMMARY_OF_CHARGES')}
					</h4>
				</Row>}
				{this.props.selectedFlight.to_city.map((item,i) => 
				<div key={i}>
					<div className="" id="">
						<div className="flight-sum-bar">
							<div className="main-body">
								{/* <div className="dashed-line"></div>
								<div>
									<div className="city-name overflow-elipsis">
										<span className="ball"></span>
										{this.props.selectedFlight.flight_info[i].dep_airport[0]}
										{'  - '} 
										{this.props.selectedFlight.flight_info[i].dep_town[0]}
									</div>
									<div className="font14 text-light-gray">
										{moment(this.props.selectedFlight.dep_flight_date[i],'DDMMM').format('MMM D')}
										{', '}
										{this.props.selectedFlight.dep_flight_time[i][0].slice(0,2)+":"+this.props.selectedFlight.dep_flight_time[i][0].slice(2,4)}
									</div>
								</div>

								<div className="">
									<div className="city-name overflow-elipsis">
										<span className="ball filled"></span>
										{this.props.selectedFlight.flight_info[i].arr_airport[this.props.selectedFlight.flight_info[i].arr_town.length-1]}
										{' - '}
										{this.props.selectedFlight.flight_info[i].arr_town[this.props.selectedFlight.flight_info[i].arr_town.length-1]}
									</div>
									<div className="font14 text-light-gray">
									{moment(this.props.selectedFlight.arr_flight_date[i][this.props.selectedFlight.arr_flight_date[i].length-1],'DDMMM').format('MMM D')}
									{', '}
									{this.props.selectedFlight.arr_flight_time[i][this.props.selectedFlight.arr_flight_time[i].length-1].slice(0,2)+":"+this.props.selectedFlight.arr_flight_time[i][this.props.selectedFlight.arr_flight_time[i].length-1].slice(2,4)}
									</div>
								</div>
								<div
									className="displayFunction"
									id="thisone"
									onClick={e => {
										this.toggleDetails(e, i);
									}}>
									<FontAwesomeIcon icon={this.state.addInfo[i]?faMinusSquare:faPlusSquare} className="highlight font18" />
								</div>

								<div className="flight-details-v">
									<div className="text-light-gray">
										<div>{t('TITLE_FILTER_FLIGHTS_DURATION')}{' : '}
											{Math.floor(this.props.selectedFlight.total_mins[i]/60)}{t('DURATION_H')}{' '}
											{this.props.selectedFlight.total_mins[i]%60}{t('DURATION_M')}
										</div>
										{this.props.selectedFlight.flight_info[i].dep_airport.length>1 &&
										<div>
											<div>{t('AIRPORT_STOP')}{' : '}</div>
											{this.props.selectedFlight.flight_info[i].dep_airport.map((item,x) => (
												<div key={i+x}>
													<div className="padding-lr10">
														{x!==0 && <span>{x + '. '}</span>}
														{this.props.selectedFlight.flight_info[i].arr_airport[x-1] && item!==this.props.selectedFlight.flight_info[i].arr_airport[x-1] &&
															<span>
																<FontAwesomeIcon icon={faExclamationTriangle} />
																{' '}
																{t('CHANGE_OF_AIRPORT')}
																{' : '}
																{this.props.selectedFlight.flight_info[i].arr_airport[x-1]}
																{' '}
																<FontAwesomeIcon icon={faArrowRight} />
																{' '}
															</span>
														}
														{x!==0 && item}
													</div>
												</div>
											))}
										</div>}
									</div>
								</div> */}
							</div>
						</div>
					</div>
					<div className="clearfix margin-b10"></div>
				</div>
			)}
			<Row>
				<Col xs={12} className="padding-l0">{t('TOTAL_AIRFARE')}</Col>
				<Col xs={12} className="font22 cursor-pointer padding-l0">
					<span className="margin-r10"><strong>{parseFloat(this.props.selectedFlight.TOTAL_PRICE).toLocaleString(this.props.lang.locale.replace('_','-')) }</strong>{' '}{this.props.selectedFlight.currency}</span>
				</Col>
			</Row>
			<div className="text-light-gray">
				{numPeople}{' '}
				{t('ALL_TRAVELERS')}
			</div>
			<Row>
				<Col xs={{offset: 1, span: 11}}>
					<FlightPriceDetails {...this.props} numPeople = {numPeople} />
				</Col>
			</Row>
			<div className="total-price-sum">
				<div className="">
					<div>{t('TOTAL_TO_PAY')}</div>
					<div className="font28">
						<strong>
							<span id="total_price3">
							{this.props.bookForm.services
								? (parseFloat(this.props.selectedFlight.TOTAL_PRICE) +
									Object.values(this.props.bookForm.services).reduce(
										(a, b) => parseFloat(a) + parseFloat(b)
									)).toLocaleString(this.props.lang.locale.replace('_','-')) 
								: parseFloat(this.props.selectedFlight.TOTAL_PRICE).toLocaleString(this.props.lang.locale.replace('_','-')) }
							{' '}</span>
						</strong>
						<span>{this.props.selectedFlight.currency}</span>
					</div>
				</div>
			</div>
		</Card>
		)
	}
}

export default FlightVertical