import initialStateSearchbar from './initialStateSearchbar';
import {
    SET_SB_FOCUS,
    SET_SB_WHERE,
    SET_SB_INDEX,
    SET_SB_MODIFY
} from '../actions/allActions';

export default function barRed(state = initialStateSearchbar, action) {
    switch (action.type) {
        case SET_SB_FOCUS:
            return { ...state, sbFocus: action.sbFocus };
        case SET_SB_WHERE:
            return { ...state, sbWhere: action.sbWhere };
        case SET_SB_INDEX:
            return { ...state, sbIndex: action.sbIndex };
        case SET_SB_MODIFY:
                return { ...state, sbModify: action.sbModify };
        default:
            return state;
        }

}