import React, { Component } from 'react';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Image from 'react-bootstrap/Image';
import Container from 'react-bootstrap/Container';

class MainMenu extends Component {

render() {
    const { t } = this.props;
    let menu = [
        {title: t('MENU_CAR_UP'), link: t('MENU_CAR_URL'), image: "/images/icn/icn-car-rental.png"},
        {title: t('MENU_TRANSFER_UP'), link: t('MENU_TRANSFER_URL'), image: "/images/icn/icn-transfer.png"},
        {title: t('MENU_HOTELSUPER_UP'), link: t('MENU_HOTELSUPER_URL'), image: "/images/icn/icn-hotel.png"},
        {title: t('MENU_DRIVE_UP'), link: t('MENU_DRIVE_URL'), image: "/images/icn/icn-fly-drive.png"},
        {title: t('MENU_HOTELSHOP_UP'), link: t('MENU_HOTELSHOP_URL'), image: "/images/icn/icn-sky-holidays.png"},
        {title: t('MENU_ATTRACTION_UP'), link: t('MENU_ATTRACTION_URL'), image: "/images/icn/icn-tickets.png"},
        {title: t('SKYTOURS_BLOG'), link: t('SKYTOURS_BLOG'), image: "/images/icn/icn-blog.png"}
    ]
    return(
        <div className={this.props.isOpen?"d-block":"d-none"}>
            <div className="margin-t20">
                <Container>
                    <Row>
                    {menu.map((item,i) => (
                        <Col xs={12} lg key={i} className="padding-lr15">
                            <a href={item.link} target="_blank" rel="noopener noreferrer" className="text-white min-width-200px margin-lr-auto text-decoration-none">
                                <Row className="d-none d-lg-block padding-tb10">
                                    <Col lg={12} className="padding-lr0 height-40">
                                        <Image src={item.image} />
                                    </Col>
                                    <Col lg={12} className="padding-lr0 margin-t-4">
                                        {item.title}
                                    </Col>
                                </Row>
                                <Row className="d-block d-lg-none font-weight-bold padding-tb10 margin-b5">
                                    <Col xs={12} className="text-left">
                                        <div className="width-50px float-left"><Image src={item.image} className="margin-b5"/></div>
                                        <div className="float-left">{item.title}</div>
                                    </Col>
                                </Row>
                            </a>
                        </Col>
                    ))}
                    </Row>
                </Container>
            </div>
        </div>
        );
    }

}

export default MainMenu;

