import React, { Component } from "react"
import moment from "moment"
import axios from "axios"
import SearchBar from "./SearchBar"
import cloneDeep from "lodash/cloneDeep"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faPlane, faCalendarAlt, faUsers, faSearch } from "@fortawesome/free-solid-svg-icons"
import { CITY_AC } from "../common/const"

class SearchInfo extends Component {
    constructor(props) {
        super(props)
        this.isLoaded = false
    }

    setCityNames(tempFlightsSearchInfo, index, where, allValues) {
        let storeIndex = index
        index++
        if (storeIndex < allValues.length) {
            axios
                .get(CITY_AC + allValues[storeIndex] + "&hl=" + this.props.lang.value)
                .catch(err => {
                    this.setCityNames(tempFlightsSearchInfo, index, where, allValues)
                })
                .then(response => {
                    if (typeof response !== "undefined" && response !== null && response.status === 200) {
                        let temp = ""
                        temp = response.data.filter(function(o) {
                            return o.value === allValues[storeIndex]
                        })
                        for (let i = 0; i < tempFlightsSearchInfo.destVal.length; i++) {
                            if (temp !== "" && temp[0].value === tempFlightsSearchInfo.dest[i].to.value) {
                                tempFlightsSearchInfo.dest[i].to = temp[0]
                                tempFlightsSearchInfo.destVal[i].to =
                                    temp[0].value + " - " + temp[0].name + " (" + temp[0].cc + ")"
                            }
                            if (temp !== "" && temp[0].value === tempFlightsSearchInfo.dest[i].from.value) {
                                tempFlightsSearchInfo.dest[i].from = temp[0]
                                tempFlightsSearchInfo.destVal[i].from =
                                    temp[0].value + " - " + temp[0].name + " (" + temp[0].cc + ")"
                            }
                        }
                        this.setCityNames(tempFlightsSearchInfo, index, where, allValues)
                    }
                })
        } else {
            // console.log('loaded')
            // console.log(tempFlightsSearchInfo)
            this.props.appActions.setFlightsSearchInfo(cloneDeep(tempFlightsSearchInfo))
            this.props.appActions.setSelDest(cloneDeep(tempFlightsSearchInfo.dest))
            this.props.appActions.setValDest(cloneDeep(tempFlightsSearchInfo.destVal))
        }
    }

    componentDidUpdate() {
        if (!this.isLoaded && this.props.flightsSearchInfo && this.props.flightsSearchInfo.destVal) {
            let allValues = []
            for (let i = 0; i < this.props.flightsSearchInfo.destVal.length; i++) {
                if (
                    !allValues.includes(this.props.flightsSearchInfo.destVal[i].to) &&
                    this.props.flightsSearchInfo.destVal[i].to.length < 5
                )
                    allValues.push(this.props.flightsSearchInfo.destVal[i].to)
                if (
                    !allValues.includes(this.props.flightsSearchInfo.destVal[i].from) &&
                    this.props.flightsSearchInfo.destVal[i].from.length < 5
                )
                    allValues.push(this.props.flightsSearchInfo.destVal[i].from)
            }
            if (allValues.length > 0) {
                this.setCityNames(cloneDeep(this.props.flightsSearchInfo), 0, "from", allValues)
                this.isLoaded = true
            }
        }
    }

    render() {
        const { t } = this.props;
        return (
            <div>
                {this.props.flightsSearchInfo &&
                    this.props.flightsSearchInfo.url &&
                    this.props.flightsSearchInfo.type && (
                        <div className="bar-holder">
                            <div className="bar-style shadow text-left font14">
                                <div
                                    className="position-relative cursor-pointer margin-15px"
                                    onClick={() => this.props.appActions.setSbModify(!this.props.sbModify)}>
                                    <div className="modify-search">
                                    <span className="accent-light-color padding-lr8">{t('MODIFY_SEARCH')}</span>
                                        <FontAwesomeIcon icon={faSearch} className="accent-light-color" />
                                        
                                    </div>
                                    {this.props.flightsSearchInfo.dest.map((item, i) => (
                                        <div key={i}>
                                            {((this.props.flightsSearchInfo.type === "one" && i === 0) ||
                                                this.props.flightsSearchInfo.type !== "one") && (
                                                <div className="margin-b10">
                                                    <div className="float-left shadow-color">
                                                        <span className="margin-r10 d-inline-block">
                                                            {this.props.flightsSearchInfo.destVal[i] &&
                                                                this.props.flightsSearchInfo.destVal[i].from}
                                                        </span>
                                                        <FontAwesomeIcon
                                                            icon={faPlane}
                                                            className="margin-r10 svg"
                                                        />
                                                        <span className="margin-r10 d-inline-block" >
                                                            {this.props.flightsSearchInfo.destVal[i] &&
                                                                this.props.flightsSearchInfo.destVal[i].to}
                                                        </span>
                                                    </div>
                                                    <div className="float-left">
                                                        <FontAwesomeIcon
                                                            icon={faCalendarAlt}
                                                            className="margin-r10 svg"
                                                        />
                                                        {moment(this.props.flightsSearchInfo.dates[i]).format(
                                                            "L"
                                                        )}
                                                    </div>
                                                    <div className="clearfix" />
                                                </div>
                                            )}
                                        </div>
                                    ))}
                                    <div className="float-left">
                                        <FontAwesomeIcon icon={faUsers} className="margin-r10 svg" />
                                        <span className="margin-r10">{t('NUMBER_BLOCK_PRICE')}:</span>
                                        {this.props.flightsSearchInfo.people.adult +
                                            this.props.flightsSearchInfo.people.child +
                                            this.props.flightsSearchInfo.people.infant +
                                            this.props.flightsSearchInfo.people.senior +
                                            this.props.flightsSearchInfo.people.youth}
                                    </div>
                                    <div className="clearfix" />
                                </div>
                                {this.props.sbModify && (
                                    <div>
                                        <hr />
                                        <SearchBar {...this.props} />
                                    </div>
                                )}
                            </div>
                        </div>
                    )}
            </div>
        )
    }
}

export default SearchInfo