let outD = moment().add(7, 'days');
let inD = moment().add(12, 'days');

export default {
    gMapLoaded: false,
    routes: 'none',
    timeStamp: moment(),
    selDest: [{from: null, to: null},{from: null, to: null}],
    selDates: [outD,inD],
    numPeople: DEFAULT_NUM_PEOPLE,
    valueDest: [{from: '', to: ''},{from: '', to: ''}],
    tripType: "round",
    alert: false
}