import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import Header from "./Header";
import Routes from './Routes';
import Container from 'react-bootstrap/Container';
import Card from 'react-bootstrap/Card';
import Image from 'react-bootstrap/Image';

class ContentConfirmation extends Component {

    render() {
        const { t } = this.props;
        return (
            <div>
               <Header {...this.props} />
               <Routes {...this.props} />
                <div className="confirmation-back info-pages-top">
                    <Container className="text-left">
                        <Card className="info-pages-shadow margin-b50 padding-lr15 padding-tb50">
                            <div className="width-100 text-center">
                                <Image src="/images/plane-icon.png" alt="icon" title="icon" className="padding-tb15 margin-b50" />
                                <h1>Thank you for trusting us!</h1>
                                <p>{t('BOOKING_SECTION')}</p>
                            </div>
                        </Card>
                    </Container>
                </div>
            </div>
        )
    }
}

export default withRouter(ContentConfirmation);