
import differenceWith from 'lodash/differenceWith';
import isEqual from 'lodash/isEqual';

export function filterByStops(obj, filtered, property, filters, callback) {
  let b = obj;
  if(filters[1].stops.includes(true) && obj.length>0) {
    let ifTrue = true;
    let ifTruePass = false;
    let flights = [];
    // eslint-disable-next-line array-callback-return
    b = obj.filter(function(e) {
      flights=[];
      for (let property in e) {
        if(property.includes('flight')) flights.push(property);
        if(property==='inbound') flights.push(property);
        if(property==='outbound') flights.push(property);
      }
      ifTrue = true;
      for(let j=0;j<flights.length;j++) {
        ifTruePass=false;
        if(e[flights[j]] && filters[1].stops[0] && !Array.isArray(e[flights[j]].fli_inf)) {
          ifTruePass=true;
        }
        if(e[flights[j]] && e[flights[j]].fli_inf && Array.isArray(e[flights[j]].fli_inf)) {
          if(filters[1].stops[1] && e[flights[j]].fli_inf.length === 2) {
            ifTruePass=true;
          }
          if(filters[1].stops[2] && e[flights[j]].fli_inf.length > 2) {
            ifTruePass=true;
          }
        } 
        ifTrue = ifTrue && ifTruePass;
      }
      if(ifTrue) return e;
    });
  }
  b = callback(b, filtered, 'price', filters, filterByDeparture);
  return b;
}

export function filterByPrice(obj, filtered, property, filters, callback) {
  let b = obj;
    if(filters[2].price && obj.length>0) {
      b=[];
      let a = obj.filter(function(e) {
        return (e.total_price>=filters[2].priceSlider[0] && e.total_price<=filters[2].priceSlider[1])
      });
      b = b.concat(a);
    }
  b = callback(b, filtered, 'airlines', filters, filterByDuration);
  return b;
}

export function filterByDeparture(obj, filtered, property, filters, callback) {
  let b = obj;
    if(filters[3].out && obj.length>0 && !obj[0].hasOwnProperty('flight3')) {
      let dir1 = obj[0].hasOwnProperty('outbound')?'outbound':'flight1';
      let dir2 = obj[0].hasOwnProperty('inbound')?'inbound':obj[0].hasOwnProperty('flight2')?'flight2':false;
      b=[];
      let a = obj.filter(function(e) {
        return (
          (e[dir1].fli_inf.hasOwnProperty('departure_time') && parseInt(e[dir1].fli_inf.departure_time)/100>=filters[3].out[0] && parseInt(e[dir1].fli_inf.departure_time)/100<=filters[3].out[1]) ||
          (!e[dir1].fli_inf.hasOwnProperty('departure_time') && parseInt(e[dir1].fli_inf[0].departure_time)/100>=filters[3].out[0] && parseInt(e[dir1].fli_inf[0].departure_time)/100<=filters[3].out[1])
        ) && (
          dir2?
          ((e[dir2].fli_inf.hasOwnProperty('departure_time') && parseInt(e[dir2].fli_inf.departure_time)/100>=filters[3].in[0] && parseInt(e[dir2].fli_inf.departure_time)/100<=filters[3].in[1]) ||
          (!e[dir2].fli_inf.hasOwnProperty('departure_time') && parseInt(e[dir2].fli_inf[0].departure_time)/100>=filters[3].in[0] && parseInt(e[dir2].fli_inf[0].departure_time)/100<=filters[3].in[1]))
          :true
        )
      });
      b = b.concat(a);
    }
  b = callback(b, filtered, 'duration', filters, filterByAirlines);
  return b;
}

export function filterByDuration(obj, filtered, property, filters, callback) {
  let b = obj;
    if(obj.length>0 && !obj[0].hasOwnProperty('flight3')) {
      let dir1 = obj[0].hasOwnProperty('outbound')?'outbound':'flight1';
      let dir2 = obj[0].hasOwnProperty('inbound')?'inbound':obj[0].hasOwnProperty('flight2')?'flight2':false;
      b=[];
      let a = obj.filter(function(e) {
        return (
          (e.hasOwnProperty('total_minutes_'+dir1) && e['total_minutes_'+dir1]<=filters[5].totalTimeOut) 
        ) && (
         dir2?
          (e.hasOwnProperty('total_minutes_'+dir2) && e['total_minutes_'+dir2]<=filters[5].totalTimeIn) 
          :true
        )
      });
      b = b.concat(a);
    }
    b = callback(b, filtered, 'airlines', filters, filterByFlight);
  return b;
}

export function filterByAirlines(obj, filtered, property, filters, callback) {
  let b = obj;
    if(obj.length>0) {
      let flights = [];
      let inversedFilter = differenceWith(filters[6].airlinesConst, filters[6].airlines, isEqual);
      // eslint-disable-next-line array-callback-return
      let a = obj.filter(function(e) {
        flights=[];
        for (let property in e) {
          if(property.includes('flight')) flights.push(property);
          if(property==='inbound') flights.push(property);
          if(property==='outbound') flights.push(property);
        }
        for(let j=0;j<flights.length;j++) {
          if(e[flights[j]] && e[flights[j]].fli_inf && Array.isArray(e[flights[j]].fli_inf)) {
            for(let i=0;i<e[flights[j]].fli_inf.length;i++) {if(inversedFilter.includes(e[flights[j]].fli_inf[i].airline_code)) return e}} 
          else if(e[flights[j]] && e[flights[j]].fli_inf && !Array.isArray(e[flights[j]].fli_inf)) return inversedFilter.includes(e[flights[j]].fli_inf.airline_code)
        }
      });
      b = differenceWith(obj, a, isEqual);
    }
    b = callback(b, filtered, 'flight', filters, filterIt);
  return b;
}

export function filterByFlight(obj, filtered, property, filters, callback) {
  let b = obj;
    if(obj.length>0 && !obj[0].hasOwnProperty('flight3')) {
      let dir1 = obj[0].hasOwnProperty('outbound')?'outbound':'flight1';
      let dir2 = obj[0].hasOwnProperty('inbound')?'inbound':obj[0].hasOwnProperty('flight2')?'flight2':false;
      b=[];
      b = obj.filter(function(e) {
        return (filters[7].selOut===0 && filters[7].selIn===0)?true:
          (filters[7].selOut!==0?isEqual(e[dir1], filters[7].selOut):isEqual(e[dir2], filters[7].selIn))
      });
    }
    b = callback(b, filtered);
  return b;
}

export function filterIt(filteredNew, filtered) {
  if (filteredNew.length === 0) return [filtered, 'strict'];
  else return [filteredNew, 'ok']
}


export function filterAll(flights, filteredFlights, filters) {
    let temp = flights.alt_inf;
    let temp1 = filteredFlights.length > 0 ? filteredFlights : flights.alt_inf;
    return filterByStops(temp, temp1, 'stops', filters, filterByPrice);
}
  