import React, { Component } from 'react';
import Dropdown from 'react-bootstrap/Dropdown';
// import Button from 'react-bootstrap/Button';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUsers, faMinusCircle, faPlusCircle } from '@fortawesome/free-solid-svg-icons';
import { PEOPLE } from '../common/const';

class NumberPeople extends Component {
    constructor(props) {
        super(props);
        this.managePeople = this.managePeople.bind(this);
        this.getNumberPassangers = this.getNumberPassangers.bind(this);
    }

    getNumberPassangers() {
        return this.props.numPeople.adult + 
            this.props.numPeople.youth +
            this.props.numPeople.child +
            this.props.numPeople.infant +
            this.props.numPeople.senior       
    }

    managePeople(add, which) {
        let temp = Object.assign({}, this.props.numPeople);
        if(add) {
            if(temp[which]<10) ++temp[which];
        } else {
            if(temp[which]>0) --temp[which];
        }
        this.props.appActions.setNumPeople(temp); 
    }

    render() {
        const { t } = this.props;
        return(
            <div>
                {this.props.numPeople &&
                    <Dropdown>
                        <Dropdown.Toggle id="dropdownPeople" className="dropdown-1">
                            <FontAwesomeIcon icon={faUsers} className="margin-r10 text-light-gray" />
                            <span className="margin-r10"></span>
                            <span>{this.getNumberPassangers()}</span>
                        </Dropdown.Toggle>
                        <Dropdown.Menu className="min-width-100-pcent">
                            {PEOPLE.map((item,i) => 
                                <Col cs={12} key={i} style={{minWidth: '280px'}}>
                                    <Row className="height-60px">
                                        <Col xs={7} className="padding-tb15 text-center">
                                            <span className="text-light-gray">
                                                <FontAwesomeIcon icon={item.icon} className="font22 text-blue" />{' '}
                                                <div className="font12 text-light-gray">{t(item.age).replace('<br>',' ')}</div>
                                            </span>
                                        </Col>
                                        <Col xs={5} className="text-center padding-tb15">
                                            <FontAwesomeIcon icon={faMinusCircle} className={this.props.numPeople[item.which]!==0?"fa-2x float-left text-blue cursor-pointer":"fa-2x float-left text-lighter-gray"} onClick={()=>{this.managePeople(false, item.which)}}/>
                                            <div className="float-left width-30px padding-tb5">{this.props.numPeople[item.which]}</div>
                                            <FontAwesomeIcon icon={faPlusCircle} className={this.props.numPeople[item.which]!==10?"fa-2x float-left text-blue cursor-pointer":"fa-2x float-left text-lighter-gray"} onClick={()=>{this.managePeople(true, item.which)}}/>
                                        </Col>
                                    </Row>
                                </Col>
                            )}
                            <div className="clearfix padding-tb5"/>
                        </Dropdown.Menu>
                    </Dropdown>
                }
            </div>
        )
    }
}

export default NumberPeople;