import initialStateLocalization from './initialStateLocalization';
import {
    CHANGE_LANG,
    CHANGE_CURR,
    SET_USERINFO
} from '../actions/allActions';

export default function locRed(state = initialStateLocalization, action) {
    switch (action.type) {
        case CHANGE_LANG:
                return { ...state, lang: action.lang };
        case CHANGE_CURR:
            return { ...state, curr: action.curr };
        case SET_USERINFO:
            let parameter = Object.keys(action.param)[0];
            let userInfoFull = state.userInfo;
            userInfoFull[parameter] = action.param[parameter];
            return { ...state, userInfo: userInfoFull };
        default:
            return state;
        }

}