import React, { Component } from "react"
import moment from "moment"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import Image from "react-bootstrap/Image"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import {
    faSuitcase,
    faClock,
    faArrowRight,
    faExclamationTriangle,
    faPlane,
    faPlay
} from "@fortawesome/free-solid-svg-icons"

class AdditionalInfo extends Component {
    render() {
        const { t } = this.props
        let flightDetails = []
        if (!Array.isArray(this.props.flight[this.props.direction].fli_inf)) {
            flightDetails[0] = this.props.flight[this.props.direction].fli_inf
        } else {
            flightDetails = this.props.flight[this.props.direction].fli_inf
        }
        return (
            <Col xs={12} className="margin-b20 fade-it-in-1 flight-info-open">
                <Row>
                    {flightDetails.map((item, i) => (
                        <Col xs={12} key={i}>
                            <Row>
                                {item.ground_time && parseInt(item.ground_time) > 0 && (
                                    <Col xs={12} className="">
                                        <Col xs={12} className="">
                                            <Col xs={12} sm={9} className="layover">
                                                <Row>
                                                    <Col xs={12} className="text-left padding-lr5">
                                                        {/*<FontAwesomeIcon
                                                            icon={faMapMarkerAlt}
                                                            className="margin-5"
                                                            />*/}
                                                        <b>
                                                            {flightDetails[i - 1] &&
                                                            item.departure_airport !==
                                                                flightDetails[i - 1].arrival_airport ? (
                                                                <span>
                                                                    <FontAwesomeIcon
                                                                        icon={faExclamationTriangle}
                                                                        className="font14 color-red"
                                                                        style={{verticalAlign: "baseline"}}
                                                                    />
                                                                    <span className="font-weight-bold margin-5">
                                                                        {t("CHANGE_OF_AIRPORT")}:
                                                                    </span>
                                                                </span>
                                                            ) : (
                                                                <span className="margin-5">
                                                                    {t("AIRPORT_STOP")}:
                                                                </span>
                                                            )}
                                                        </b>
                                                        <b>
                                                            {flightDetails[i - 1] &&
                                                            item.departure_airport !==
                                                                flightDetails[i - 1].arrival_airport ? (
                                                                <span>
                                                                    <span>
                                                                        {flightDetails[i - 1].arrival_airport}
                                                                    </span>{' '}
                                                                    <span>
                                                                        <FontAwesomeIcon
                                                                            icon={faArrowRight}
                                                                        />
                                                                    </span>{' '}
                                                                    <span>{item.departure_airport}</span>
                                                                    <div className="font12 font-weight-normal text-danger maxw400 margin-t4 margin-b10">{t('DIFF_ARR_DEP_AIRPORT')}</div>
                                                                </span>
                                                            ) : (
                                                                <span>{item.departure_airport}</span>
                                                            )}
                                                        </b>
                                                    </Col>

                                                    <Col xs={12} className="text-left svg ">
                                                        <FontAwesomeIcon icon={faClock} />
                                                        {" " +
                                                            item.ground_time.slice(0, 2) +
                                                            t("DURATION_H") + " " +
                                                            item.ground_time.slice(2, 4) +
                                                            t("DURATION_M")}
                                                    </Col>
                                                </Row>
                                            </Col>
                                        </Col>
                                    </Col>
                                )}
                                <Col xs={12} sm={2} md={3} style={{height: 0}}>
                                    <Row className="xs-relative">
                                        
                                        <Col xs sm={12} className="airline">
                                            {this.props.airlines.hasOwnProperty(item.airline_code) &&
                                                this.props.airlines[item.airline_code]}
                                            <br />
                                            <FontAwesomeIcon icon={faPlay}  />
                                            <span>
                                                {item.airline_code}-{item.flight_num}
                                            </span>
                                        </Col>
                                        <Col  sm={12} className="logos">
                                            <Image 
                                            src={'/images/airlogos/'+item.airline_code.toLowerCase()+'.gif'}
                                            onError={e => {e.target.src = '/images/airlogos/_noimg.gif'}}
                                            className="float-left margin-b10 margin-lr-2"
                                        />
                                        </Col>
                                    </Row>
                                </Col>
                                <Col xs={12} sm={10} md={9} className="section">
                                    <Row>
                                        <Col xs={6} className="margin-b5">
                                            <span>
                                                {moment(item.departure_date).format("MMM D")},{" "}
                                                {item.departure_time.slice(0, 2) +
                                                    ":" +
                                                    item.departure_time.slice(2, 4)}
                                            </span>
                                        </Col>
                                        <Col xs={6} className="text-right margin-b5">
                                            {item.dep_arrival_date_dif !== "0" && (
                                                <span >
                                                   {moment(item.arrival_date).format("MMM D")},{" "}
                                               
                                                </span>
                                            )}
                                            <span>
                                                {item.arrival_time.slice(0, 2) +
                                                    ":" +
                                                    item.arrival_time.slice(2, 4)}{" "}
                                            </span>
                                        </Col>
                                        <Col xs={3} sm={2} md={3} className="padding-r0 margin-b5">
                                            <span
                                                className={
                                                    flightDetails[i - 1] &&
                                                    item.departure_airport !==
                                                        flightDetails[i - 1].arrival_airport
                                                        ? "text-danger"
                                                        : ""
                                                }>
                                                <b>{item.departure_airport}</b>
                                            </span>
                                        </Col>
                                        <Col
                                            xs={6}
                                            sm={8}
                                            md={6}
                                            className="text-center font12 padding-lr8 text-light-gray">
                                            <FontAwesomeIcon
                                                icon={faPlane}
                                                className=" font20 padding-lr2 text-blue move-plane"
                                            />
                                        </Col>
                                        <Col xs sm={2} md={3} className="text-right padding-l0">
                                            <span
                                                className={
                                                    flightDetails[i + 1] &&
                                                    item.arrival_airport !==
                                                        flightDetails[i + 1].departure_airport
                                                        ? " text-danger margin-b5"
                                                        : " margin-b5"
                                                }>
                                                <b>{item.arrival_airport}</b>
                                            </span>
                                        </Col>
                                        <Col xs={6} className="font12 font-weight-light">
                                            {item.departure_city}
                                        </Col>
                                        <Col xs={6} className="font12 font-weight-light text-right">
                                            {item.arrival_city}
                                        </Col>

                                        <Row className="padding-lr10 margin-t10">
                                            <Col xs={12}>
                                                <div className="text-light-gray">
                                                    <span className="margin-r10">
                                                        <FontAwesomeIcon icon={faClock} />
                                                    </span>
                                                    {item.elapsed_flight_time.slice(0, 2) +
                                                        t("DURATION_H") + " " +
                                                        item.elapsed_flight_time.slice(2, 4) +
                                                        t("DURATION_M")}
                                                </div>
                                            </Col>
                                            <Col xs={1} className="text-light-gray">
                                                <FontAwesomeIcon icon={faSuitcase} />
                                            </Col>
                                            <Col xs className="text-light-gray">
                                                {typeof item.baggage_dimension === "string" &&
                                                    item.baggage_dimension}
                                                {typeof item.baggage_included === "string" &&
                                                    item.baggage_included}
                                            </Col>
                                        </Row>
                                    </Row>
                                </Col>
                            </Row>
                        </Col>
                    ))}
                </Row>
            </Col>
        )
    }
}

export default AdditionalInfo