import React, { Component } from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import Image from 'react-bootstrap/Image';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faExclamationTriangle } from '@fortawesome/free-solid-svg-icons';


class InfoMsg extends Component {
//   constructor(props) {
//     super(props);
//   }

closeModal = (msg) => {
    this.props.appActions.setInfoMsg('none');
    if(this.props.toHome && msg==="no_flight") this.props.appActions.setRouteInfo('home');
}

render() {
    const { t } = this.props;
    return (
        <div>
            <Modal show={this.props.infoMsg==='no_flight'} onHide={()=>this.closeModal()}>
                <div><Image src="/images/puzzle.jpg" alt="not found" title="flight not found" fluid /></div>
                <Modal.Body className="bg-warning">
                    <div className="padding-tb15 text-center" dangerouslySetInnerHTML={{__html: t('BOOKING_EXPIRIES_TEXT2')}}></div>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="outline-primary" className="margin-lr-auto" onClick={()=>this.closeModal("no_flight")}>
                        <div className="padding-lr15">{t('BUTTON_CLOSE')}</div>
                    </Button>
                </Modal.Footer>
            </Modal>
            <Modal show={this.props.infoMsg==='no_results'} onHide={()=>this.closeModal()}>
                <Modal.Body className="bg-warning">
                    <div className="padding-tb15 text-center">
                        <FontAwesomeIcon icon={faExclamationTriangle} className="color-red fa-5x margin-b20" />
                        <div className="font-weight-bold">{t('ERROR_NOT_FOUND')}</div>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="outline-primary" className="margin-lr-auto" onClick={()=>this.closeModal("no_results")}>
                        <div className="padding-lr15">{t('BUTTON_CLOSE')}</div>
                    </Button>
                </Modal.Footer>
            </Modal>
        </div>
    )   
  }
}

export default InfoMsg;
