import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Image from 'react-bootstrap/Image';

class WhySkyTours extends Component {

render() {
    let texts = [
        'we check our prices daily to ensure we offer the best value',
        'compare and book 100s of airlines, 1000s of hotels and car hire worldwide',
        'tailor your trip in one visit to sky-tours.com, your one-stop travel shop',
        'our call centre provides full support before, during and after your trip',
        'millions of customers since 2002 can\'t be wrong'
    ]
    return(
            <Container className="padding-tb100">
                <h1>Why Choose Sky-Tours?</h1>
                <br/>
                <Row>
                {texts.map((item,i) => (
                    <Col key={i} className="padding-lr15 padding-tb15">
                        <Image src={'/images/icons/icon'+(i+1)+'.png'}/>
                        <br/>
                        <div className="font14 text-light-gray maxw200 margin-lr-auto">{texts[i]}</div>
                    </Col>
                ))}
                </Row>
            </Container>
        );
    }

}

export default WhySkyTours;

