import React, { Component } from "react"
import moment from "moment"
import FlightInfoDetails from "./FlightInfoDetails"
import Card from "react-bootstrap/Card"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import Image from "react-bootstrap/Image"
import Button from "react-bootstrap/Button"
import Form from "react-bootstrap/Form"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faPlane, faClock, faHandPaper, faAngleDoubleRight } from "@fortawesome/free-solid-svg-icons"
import { faPlusSquare, faMinusSquare } from "@fortawesome/free-regular-svg-icons"
import { filterAll } from "../common/utilsFilters"
import isEqual from "lodash/isEqual"
import { scroller } from "react-scroll"

class FlightInfo extends Component {
    constructor(props) {
        super(props)
        this.state = {
            addInfo: {}
        }
    }

    setAddInfo = direction => {
        let addInfoTemp = this.state.addInfo
        if (addInfoTemp.hasOwnProperty(direction)) delete addInfoTemp[direction]
        else addInfoTemp[direction] = true
        this.setState({
            addInfo: addInfoTemp
        })
    }

    renderMoreInfo = open => {
        const { t } = this.props
        return (
            <div className="flight-info-detail">
                <FontAwesomeIcon
                    icon={open ? faMinusSquare : faPlusSquare}
                    className="cursor-pointer font16"
                />{" "}
                <span className="margin-r5 ">{t("DETAILS").toLowerCase()}</span>
            </div>
        )
    }

    filterFlight = direction => {
        let temp = this.props.filters.slice()
        if (direction === "outbound") {
            if (temp[7].selOut === 0) {
                temp[7] = { selOut: this.props.flight[direction], selIn: 0 }
            } else if (temp[7].selOut !== 0) {
                temp[7] = { selOut: 0, selIn: 0 }
            }
        } else {
            if (temp[7].selIn === 0) {
                temp[7] = { selOut: 0, selIn: this.props.flight[direction] }
            } else if (temp[7].selIn !== 0) {
                temp[7] = { selOut: 0, selIn: 0 }
            }
        }
        let f = filterAll(this.props.flights, this.props.filteredFlights, temp)
        this.props.appActions.setFilteredFlights(
            f[0].length === this.props.flights.alt_inf.length ? [] : f[0]
        )
        if (f[1] === "strict") temp[0].msg = "strict"
        else if (f[1] === "ok") temp[0].msg = ""
        this.props.appActions.setPage(1)
        this.props.appActions.setFilters(temp)
        scroller.scrollTo("pages", {
            duration: 1000,
            delay: 100,
            smooth: true
        })
    }

    renderFlight = direction => {
        const { t } = this.props
        let flightDetails = []
        let carriers = []
        if (!Array.isArray(this.props.flight[direction].fli_inf)) {
            flightDetails[0] = this.props.flight[direction].fli_inf
            carriers.push(this.props.flight[direction].fli_inf.airline_code)
        } else {
            flightDetails = this.props.flight[direction].fli_inf
            for (let i = 0; i < this.props.flight[direction].fli_inf.length; i++) {
                if (!carriers.includes(this.props.flight[direction].fli_inf[i].airline_code))
                    carriers.push(this.props.flight[direction].fli_inf[i].airline_code)
            }
        }
        return (
            <Row className="search-container">
                {/*direction !== "outbound" && direction !== "flight1" && (
                    <Col xs={12}>
                        {<div style={{ height: "3em" }}></div>}
                    </Col>
                ) */}
                <Col xs={4} sm={4} md={4} lg={4}>
                    {/* <Row>
                        <Col xs={12} className="margin-b10 text-uppercase text-light-gray font-weight-bold">
                            {direction==='outbound'?<span className="color-green">Departure</span>:<span className="color-red">Return</span>}
                            <div className="float-right text-lighter-gray"><FontAwesomeIcon icon={faPlane} className={direction==='outbound'?"font22 color-green":"color-red font22 fa-rotate-180"} /></div>
                        </Col>
                    </Row> */}
                    <Row>
                        <Col xs={8} sm={10} className="top-logos padding-r-logos">
                            {carriers.map((item, i) => (
                                <Image
                                    key={i}
                                    src={
                                        typeof item === "string"
                                            ? "/images/airlogos/" + item.toLowerCase() + ".gif"
                                            : "/images/airlogos/_noimg.gif"
                                    }
                                    onError={e => {
                                        e.target.src = "/images/airlogos/_noimg.gif"
                                    }}
                                    className="float-left margin-b5 margin-lr-2"
                                />
                            ))}
                        </Col>
                        {this.props.flight.hasOwnProperty("outbound") &&
                            this.props.flightsSearchInfo.type !== "one" &&
                            ((isEqual(
                                this.props.flight[direction],
                                this.props.filters[7][direction === "outbound" ? "selOut" : "selIn"]
                            ) &&
                                this.props.filters[7][direction === "outbound" ? "selIn" : "selOut"] === 0) ||
                                this.props.filters[7].selIn === this.props.filters[7].selOut) && (
                                <Col xs={4} sm={2} className="checkbox">
                                    <Form>
                                        <Form.Group>
                                            <Form.Check
                                                custom
                                                type="checkbox"
                                                checked={isEqual(
                                                    this.props.flight[direction],
                                                    this.props.filters[7][
                                                        direction === "outbound" ? "selOut" : "selIn"
                                                    ]
                                                )}
                                                label=""
                                                onChange={() => this.filterFlight(direction)}
                                                id={direction + this.props.index}
                                            />
                                        </Form.Group>
                                    </Form>
                                </Col>
                            )}
                    </Row>
                </Col>

                <Col xs={8} sm={8} className="padding-r0 padding-l-logos">
                    <Row className="padding-l10">
                        {/* <Col xs={12} className="text-light-gray">
                            <FontAwesomeIcon icon={faCalendarAlt} className="margin-r10"/>
                            {moment(flightDetails[0].departure_date).format('L')}
                            <hr className="margin0 border-dotted"/>
                        </Col> */}
                        <Col xs={5} className="padding0">
                            <span className="margin-r10 font16">
                                <b>{flightDetails[0].departure_airport}</b>
                            </span>
                            <span className=" font14 svg max-content">
                                {flightDetails[0].departure_time.slice(0, 2) +
                                    ":" +
                                    flightDetails[0].departure_time.slice(2, 4)}
                            </span>
                        </Col>
                        <Col
                            xs={2}
                            className="font16 cursor-pointer padding0"
                            onClick={() => this.setAddInfo(direction)}>
                            <FontAwesomeIcon icon={faPlane} className="svg" />
                        </Col>
                        <Col xs={5} className="padding0">
                            <span className="font16 margin-r10">
                                <b>{flightDetails[flightDetails.length - 1].arrival_airport}</b>
                            </span>
                            <span className="font14 svg max-content">
                                {flightDetails[flightDetails.length - 1].arrival_time.slice(0, 2) +
                                    ":" +
                                    flightDetails[flightDetails.length - 1].arrival_time.slice(2, 4)}
                            </span>
                            <span>
                                {moment(flightDetails[flightDetails.length - 1].arrival_date).diff(
                                    moment(flightDetails[0].departure_date),
                                    "days"
                                ) > 0 && (
                                    <span
                                        className="next-day"
                                        onClick={e =>
                                            e.currentTarget.querySelector("div").classList.toggle("show")
                                        }
                                        onMouseOver={e =>
                                            e.currentTarget.querySelector("div").classList.toggle("show")
                                        }
                                        onMouseOut={e => {
                                            if (e.currentTarget.querySelector("div").classList.length) {
                                                e.currentTarget.querySelector("div").classList.toggle("show")
                                            }
                                        }}>
                                        <div>
                                            <span>
                                                Arriving{" "}
                                                {moment(
                                                    flightDetails[flightDetails.length - 1].arrival_date
                                                ).diff(moment(flightDetails[0].departure_date), "days")}{" "}
                                                day(s) later.
                                            </span>
                                        </div>
                                        +
                                        {moment(flightDetails[flightDetails.length - 1].arrival_date).diff(
                                            moment(flightDetails[0].departure_date),
                                            "days"
                                        )}
                                    </span>
                                )}
                            </span>
                        </Col>
                        {/* <Col xs={5} className="text-light-gray">{flightDetails[0].departure_city.substring(0, flightDetails[0].departure_city.indexOf(';'))}</Col>
                        <Col xs={1}></Col>
                        <Col xs={5} className="text-light-gray">{flightDetails[flightDetails.length-1].arrival_city.substring(0, flightDetails[flightDetails.length-1].arrival_city.indexOf(';'))}</Col> */}
                    </Row>
                    <Row className="margin-t10 font14">
                        {/* <Col xs={12}><hr className="padding-tb0 margin-t0 border-dotted"/></Col> */}
                        <Col xs={5} className="svg">
                            <FontAwesomeIcon icon={faHandPaper} />{' '}
                            {flightDetails.length > 1 ? (
                                <span>{flightDetails.length - 1} stop(s)</span>
                            ) : (
                                t("NONSTOP").toLowerCase()
                            )}
                        </Col>
                        <Col xs={2}></Col>
                        <Col xs={5} className="svg" style={{ paddingLeft: "3px" }}>
                            <FontAwesomeIcon icon={faClock} />{" "}
                            {Math.floor(this.props.flight["total_minutes_" + direction] / 60)}
                            {t("DURATION_H")}
                            {this.props.flight["total_minutes_" + direction] % 60}
                            {t("DURATION_M")}
                        </Col>
                        {/* <Col xs={2} className="float-right text-right text-light-gray">
                            <FontAwesomeIcon icon={faSuitcase} className="font18 text-blue margin-lr-2 cursor-pointer" />
                        </Col> */}
                    </Row>
                </Col>
                <Col xs={{span: 12}} sm={{offset: 4, span: 8}} xl={{offset: 4, span: 8}}>
                    <Col sm={{offset: 7, span: 5}}
                        className="margin-t4 margin-b18 cursor-pointer d-block z-index-100"
                        onClick={() => this.setAddInfo(direction)}>
                        {this.renderMoreInfo(this.state.addInfo[direction])}
                    </Col>
                </Col>
                {this.state.addInfo[direction] && <FlightInfoDetails {...this.props} direction={direction} />}
            </Row>
        )
    }

    handleSelect(flight) {
        //select the flight
        this.props.appActions.getFlightDetails(flight.detailslink, true)
        // flight['urlResults'] = window.location.href;
        // this.props.appActions.setSelectedFlight(flight);
        // this.props.appActions.setRouteInfo('book');
    }

    componentWillReceiveProps(nextProps) {
        if (this.props.page !== nextProps.page || this.props.filters !== nextProps.filters) {
            this.setState({
                addInfo: {}
            })
        }
    }

    render() {
        const { t } = this.props
        let flights = []
        for (let property in this.props.flight) {
            if (property.includes("flight")) flights.push(property)
        }
        flights = flights.sort()
        return (
            <Col xs={12} className="padding-tb10 text-left">
                {this.props.flight && (
                    <Card className="">
                        <Card.Body className="padding-lr10 ">
                            <Row className="parent">
                                <Col xs={12} md={8} lg={12} xl={8} >
                                    {this.props.flight.hasOwnProperty("outbound") ? (
                                        <div>
                                            {this.props.flight.outbound && this.renderFlight("outbound")}
                                            {this.props.flight.inbound && this.renderFlight("inbound")}
                                        </div>
                                    ) : (
                                        <div>
                                            {flights.map((item, i) => (
                                                <div key={i}>
                                                    {this.props.flight[item].fli_inf &&
                                                        this.renderFlight(item)}
                                                </div>
                                            ))}
                                        </div>
                                    )}
                                </Col>
                                <Col md={0} lg={0} xl={0}></Col>
                                <Col xs={12} md={4} lg={12} xl={4} className="price-select">
                                    {/*<Col xs={12} className="padding-lr0  d-xs-block d-xl-none">
                                          
                                                                        </Col>*/}
                                    <Col
                                        className={
                                            Object.keys(this.state.addInfo).length === 0 &&
                                            this.state.addInfo.constructor === Object
                                                ? "text-right"
                                                : "text-right"
                                        }>
                                        <div>
                                            <div className="text-light-gray text-lowercase">
                                                {t("TOTAL_AIRFARE")}
                                            </div>

                                            <h4 className="margin0">
                                                <span className="font-weight-bold">
                                                    {parseFloat(this.props.flight.total_price).toLocaleString(this.props.lang.locale.replace('_','-')) }
                                                </span>{" "}
                                                <span className="font16">{this.props.flight.currency}</span>
                                            </h4>
                                        <Row>
                                            <Col xs={12} xl={{offset: 1, span: 11}}>
                                                <Button
                                                    variant="success"
                                                    className="float-right accent has-glow width-100 fill margin-t10"
                                                    onClick={() => this.handleSelect(this.props.flight)}>
                                                    <span>
                                                        Select&nbsp;&nbsp;
                                                        <FontAwesomeIcon icon={faAngleDoubleRight} />
                                                    </span>
                                                </Button>
                                            </Col>
                                        </Row>
                                            <div className="clearfix"></div>
                                        </div>
                                    </Col>
                                </Col>
                            </Row>
                        </Card.Body>
                    </Card>
                )}
            </Col>
        )
    }
}

export default FlightInfo