import { combineReducers } from 'redux';
import { routerReducer } from 'react-router-redux';
import appRed from './appReducer';
import locRed from './appReducerLocalization';
import resRed from './appReducerResults';
import barRed from './appReducerSearchbar';
import formRed from './appReducerForm';
import bookFormRed from './appReducerBookForm';
import flightAddRed from './appReducerFlightAdd';
import infoRed from './appReducerInfo';

export default combineReducers({
  router: routerReducer,
  appRed: appRed, 
  locRed: locRed,
  resRed: resRed,
  barRed: barRed,
  formRed: formRed,
  bookFormRed : bookFormRed,
  flightAddRed: flightAddRed,
  infoRed: infoRed
});