import {
   BOOK_FORM,
   SET_INPUT,
   SET_PERSONAL_INPUT,
   RESET_BOOK_FORM
} from "../actions/allActions"

const initialStateForm = {
   stage: 1,
   email: { value: "", err: "" },
   repeatEmail: { value: "", err: "" },
   countryCode: { value: "", err: "" },
   phoneNumber: { value: "", err: "" },
   services: {
      //additional services
      cancel: 0, //total of flight cancel protection
      protection: 0 //total of missed flight protection
   },
   usersData: "" //query string sent for the backend 
}

export default function bookFormRed(state = initialStateForm, action) {
   switch (action.type) {
      case BOOK_FORM:
         return { ...action.payload, ...state }
      case SET_INPUT:
         return { ...state, ...action.payload }
      case SET_PERSONAL_INPUT:
         return { ...state, ...action.payload }
      case RESET_BOOK_FORM:
         return { ...state, ...action.payload }

      default:
         return state
   }
}