import React, { Component } from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSuitcase} from '@fortawesome/free-solid-svg-icons';
import { faSquare, faCheckSquare } from '@fortawesome/free-regular-svg-icons';
import { listHelper } from '../common/utils';

class Luggage extends Component {
    constructor(props) {
        super(props);
        this.state={
            opt_services: { },
            msg: ""
        }
    }

    manageLuggage = (item, i) => {
        item.index=i;
        if(this.props.bagInfo.index!==i) this.props.appActions.setBagInfo(item);
        else this.props.appActions.setBagInfo({ })
    }

    componentDidMount() {
        let opt_services = { baggage: [] };
        if(this.props.selectedFlight.flight_info.hasOwnProperty('optional_services')) {
            let tempOptServ = this.props.selectedFlight.flight_info.optional_services;
            if(this.props.selectedFlight.service === 'travelport' ||
                this.props.selectedFlight.service === 'travelfusion') {
                for(let i=0;i<tempOptServ.baggage.prices[tempOptServ.baggage.type].length;i++) {
                    opt_services.baggage.push({ 
                        text: tempOptServ.baggage.prices[tempOptServ.baggage.type][i].Text,
                        price: tempOptServ.baggage.prices[tempOptServ.baggage.type][i].Amount,
                        currency: tempOptServ.baggage.prices[tempOptServ.baggage.type][i].Currency,
                        type: "",
                        quantity: parseInt(tempOptServ.baggage.prices[tempOptServ.baggage.type][i].Text.charAt(0)),
                        weight: ""
                    });
                }
            }
            else if(this.props.selectedFlight.service === 'tripstack') {
                for(let i=0;i<tempOptServ.baggage[0].length;i++) {
                    opt_services.baggage.push({ 
                        text: tempOptServ.baggage[0][i].quantity+" "+tempOptServ.baggage[0][i].type+" bags - "+tempOptServ.baggage[0][i].quantity*tempOptServ.baggage[0][i].weight+"kg total",
                        price: tempOptServ.baggage[0][i].price,
                        currency: this.props.selectedFlight.currency_ori,
                        type: tempOptServ.baggage[0][i].type,
                        quantity: tempOptServ.baggage[0][i].quantity,
                        weight: tempOptServ.baggage[0][i].weight
                    });
                }
            }
            if(opt_services.baggage.length>0) {
                this.setState({ opt_services: opt_services });
            }
        } 
        if(opt_services.baggage.length===0) {
            this.setState({ msg: "We are not able to provide this service during the booking process. Thank you." });
        }
    }

    render() {
        return (
            <div>
                {this.props.selectedFlight && this.props.selectedFlight.flight_info[0].hasOwnProperty('carryon') && 
                    <div>
                        {this.props.selectedFlight.flight_info[0].carryon.map((item, i) => 
                            <div key={i}>
                                <Card className="border-primary cursor-pointer bg-light padding-tb15 padding-lr15">
                                    <div className="width-100 margin-b20">
                                        <FontAwesomeIcon icon={faCheckSquare} className="float-left margin-r10 font22 margin-t10 text-blue" />
                                        <FontAwesomeIcon icon={faSuitcase} className="float-left margin-r10 fa-2x text-light-gray" />
                                    </div>
                                    <div className="font18">Carry on: {item}</div>
                                    <div className="text-blue font20 font-weight-bold">included</div>
                                </Card>
                            </div>
                        )}
                    </div>
                }
                {this.props.selectedFlight && this.props.selectedFlight.flight_info.hasOwnProperty('optional_services') && 
                <Row>
                    {this.state.opt_services.hasOwnProperty('baggage') && this.state.opt_services.baggage.map((item,i) => 
                        <Col xs={12} key={i} className="margin-b10">
                            <Card 
                                className={(item.price===0 || this.props.bagInfo.index===i)?"border-primary cursor-pointer bg-light padding-tb15 padding-lr15":"cursor-pointer bg-light padding-tb15 padding-lr15"}
                                onClick={()=>this.manageLuggage(item,i)}
                            >
                                <div className="width-100 margin-b20">
                                    {(item.price!==0 
                                    && this.props.bagInfo.index!==i) && 
                                        <FontAwesomeIcon icon={faSquare} className="float-left margin-r10 font22 margin-t10 text-blue" />
                                    }
                                    {(item.price===0
                                    || this.props.bagInfo.index===i) && 
                                        <FontAwesomeIcon icon={faCheckSquare} className="float-left margin-r10 font22 margin-t10 text-blue" />
                                    }
                                    {listHelper(item.quantity).map((number,j) => 
                                        <FontAwesomeIcon key={j} icon={faSuitcase} className="float-left margin-r10 fa-2x text-light-gray" />)
                                    }
                                </div>
                                <div className="font18">{item.text}</div>
                                {item.price===0?
                                <div className="text-blue font20 font-weight-bold">included</div>:
                                <div className="text-blue font20 font-weight-bold">{item.price}{' '}{item.currency}</div>}
                            </Card>
                        </Col>
                    )}
                </Row>}
                {this.state.msg.length>0 && 
                    <div className="text-center">
                        {this.state.msg}
                    </div>
                }
            </div>
        )
}

}

export default Luggage;