import React, { Component } from "react"
import Routes from "./Routes"
import Header from "./Header"
import FlightInfoSelected from "./FlightInfoSelected"
// import FlightInfoSummary from './FlightInfoSummary';
import { withRouter } from "react-router-dom"
import Modal from "react-bootstrap/Modal";
import Card from "react-bootstrap/Card";
import Container from "react-bootstrap/Container"
import Image from "react-bootstrap/Image"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import BookForm from "./BookForm"
import FlightVertical from "./FlightVertical"
import cloneDeep from "lodash/cloneDeep"
import InfoMsg from "./InfoMsg"

// import { PEOPLE } from '../common/const';

class ContentFlight extends Component {
    constructor(props) {
        super(props);
        this.state={
            modalPriceSummary: false
        }
    }

    setModalPrice = (x) => {
        this.setState({ modalPriceSummary: x })
    }
    componentWillMount() {
        if (
            this.props.selectedFlight &&
            this.props.selectedFlight.hasOwnProperty("from_search") &&
            this.props.selectedFlight.from_search
        ) {
            let selected = cloneDeep(this.props.selectedFlight)
            selected.from_search = false
            this.props.appActions.setSelectedFlight(selected)
        } else {
            // this.props.appActions.getFlightDetails(decodeURIComponent(this.props.match.params.query).split('&&&').join('/'), false)
            this.props.appActions.getFlightDetails(decodeURIComponent(window.location.search), false);
        }
    }
    componentDidMount() {
        if (this.props.selectedFlight && this.props.selectedFlight.TOTAL_PRICE) {
            let tempNumPeople = {
                adult: parseInt(this.props.selectedFlight.search.adult),
                child: parseInt(this.props.selectedFlight.search.child),
                infant: parseInt(this.props.selectedFlight.search.infant),
                senior: parseInt(this.props.selectedFlight.search.senior),
                youth: parseInt(this.props.selectedFlight.search.youth)
            }
            this.props.appActions.setNumPeople(tempNumPeople)
        }
    }

    render() {
        const { t } = this.props;
        return (
            <div className="confirmation-back">
                <Header {...this.props} page="flight" />
                <Routes {...this.props} />
                <InfoMsg {...this.props} toHome={true} />
                {this.props.selectedFlight && this.props.selectedFlight.TOTAL_PRICE ? (
                    <div>
                        <Modal show={this.state.modalPriceSummary} onHide={()=>this.setModalPrice(false)}>
                            <Modal.Header closeButton>
                                <Modal.Title>
                                    <h4>
                                        {t('SUMMARY_OF_CHARGES')}
                                    </h4>
                                </Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <FlightVertical {...this.props} modal={this.state.modalPriceSummary} />
                            </Modal.Body>
                        </Modal>
                        {/* <div className="main-info-holder text-left margin0">
                            <Container>
                                <Row>
                                    <Col xs={10}>
                                        <FlightInfoSummary {...this.props} />
                                    </Col>
                                </Row>
                            </Container>
                            {this.props.selectedFlight.hasOwnProperty('urlResults') 
                            && this.props.selectedFlight.urlResults.includes('search/') && 
                                <FontAwesomeIcon 
                                    icon={faAngleDoubleLeft} 
                                    className="back-to-flights font28 cursor-pointer text-blue" 
                                    onClick={() => this.props.appActions.setRouteInfo('search')}
                                />
                            }
                        </div> */}
                        <Container className="margin-t50">
                            <Col className="clearfix"></Col>
                            <Row className="margin-b50">
                                <Col xs={12} lg={8}>
                                    <Row>
                                        <FlightInfoSelected {...this.props} setModalPrice={this.setModalPrice} />
                                    </Row>
                                    <BookForm id="bookform" {...this.props} setModalPrice={this.setModalPrice} />
                                </Col>
                                <Col xs={12} lg={4} >
                                    <div className="d-none d-xl-block position-sticky-top d-md-none d-lg-block">
                                        <Card className="margin-t10 font14 ">
                                            <FlightVertical {...this.props} />
                                        </Card>
                                    </div>
                                </Col>
                            </Row>
                        </Container>
                    </div>
                ) : (
                    <div>
                        <Image src="/images/st_logo_white.svg" width="70%" className="padding-tb50" />
                    </div>
                )}
            </div>
        )
    }
}

export default withRouter(ContentFlight)