import React, { Component } from 'react';
import Pagination from 'react-js-pagination';
import { PAGES, PER_PAGE } from '../common/const';
import { scroller } from 'react-scroll';

class Pages extends Component {
  constructor(props) {
    super(props);
    this.clickPage = this.clickPage.bind(this);
  }
  clickPage(page, e) {
    this.props.appActions.setPage(page);
    // let results = document.getElementById('search-results');
    // results.scrollIntoView();
    scroller.scrollTo('pages', {
      duration: 1000,
      delay: 100,
      smooth: true
    });
  }

  render() {
    let results = [];
    if (this.props.filteredFlights && this.props.filteredFlights.length > 0) {
      results = this.props.filteredFlights;
    } else if (this.props.filteredFlights && this.props.filteredFlights.length <= 0 && this.props.flights.alt_inf.length > 0) {
      results = this.props.flights.alt_inf;
    } else results = 0;
    if (results === 0) {
      return null;
    } else {
      return (
        <Pagination
          activePage={this.props.page}
          itemsCountPerPage={PER_PAGE}
          totalItemsCount={results.length}
          pageRangeDisplayed={PAGES}
          onChange={e => this.clickPage(e)}
          linkClass=""
        />
      );
    }
  }
}
export default Pages;
