import initialState from './initialState';
import {
    RESET,
    LOADING,
    G_MAP_LOADED,
    SET_DATES,
    SET_NUM_PEOPLE,
    ROUTE_INFO,
    SET_DEST,
    SET_VAL_DEST,
    SET_TRIP_TYPE,
    SET_ALERT,
    TIME_STAMP
} from '../actions/allActions';

export default function appRed(state = initialState, action) {
    switch (action.type) {
        case 'persist/REHYDRATE':
            let newS = {};
            typeof action.payload != 'undefined'
            ? (newS = { ...action.payload.appRed })
            : (newS = state);
            return newS;
        case RESET:
            return initialState;
        case LOADING:
            return { ...state, loading: action.loading, error: false };
        case ROUTE_INFO:
            return { ...state, routes: action.routes };
        case G_MAP_LOADED:
            return { ...state, gMapLoaded: action.gMapLoaded };
        case SET_DATES:
            return { ...state, selDates: action.selDates };
        case SET_NUM_PEOPLE:
            return { ...state, numPeople: action.numPeople };
        case SET_DEST:
            return { ...state, selDest: action.selDest };
        case SET_VAL_DEST:
                return { ...state, valueDest: action.valueDest };
        case SET_TRIP_TYPE:
            return { ...state, tripType: action.tripType };
        case SET_ALERT:
            return { ...state, alert: action.alert };
        case TIME_STAMP:
            return { ...state, timeStamp: action.timeStamp };
        default:
            return state;
        }

}