import React, { Component } from 'react';
import { Range } from 'rc-slider';
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import 'rc-tooltip/assets/bootstrap.css';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Alert from 'react-bootstrap/Alert';
import Image from 'react-bootstrap/Image';
import { filterAll } from '../common/utilsFilters';

class Filters extends Component {
    constructor(props) {
        super(props)
        // this.setFilters = this.setFilters.bind(this);
        this.filterTasks = this.filterTasks.bind(this);
        this.stopsFilter = this.stopsFilter.bind(this);
        this.changePrice = this.changePrice.bind(this);
        this.priceFilter = this.priceFilter.bind(this);
        this.changeDepartureTime = this.changeDepartureTime.bind(this);
        this.departureFilter = this.departureFilter.bind(this);
        this.changeDuration = this.changeDuration.bind(this);
        this.durationFilter = this.durationFilter.bind(this);
        // this.state={
        //     refresh: false
        // }
    }

    filterTasks(temp) {
        let f = filterAll(this.props.flights, this.props.filteredFlights, temp);
        this.props.appActions.setFilteredFlights(f[0].length===this.props.flights.alt_inf.length?[]:f[0]);
        if (f[1] === 'strict') temp[0].msg = 'strict'
        else if(f[1] === 'ok') temp[0].msg = '';
        this.props.appActions.setPage(1);
        this.props.appActions.setFilters(temp);
    }

    stopsFilter = stops => {
        let temp = this.props.filters.slice();
        temp[1].stops[stops]=!temp[1].stops[stops];
        this.filterTasks(temp);
    }

    changePrice = value => {
        let temp = this.props.filters.slice();
        temp[2].priceSlider = value;
        this.props.appActions.setFilters(temp);
      };
    
    priceFilter = value => {
        let temp = this.props.filters.slice();
        temp[2].price[0] = value[0];
        temp[2].price[1] = this.props.filters[2].priceSlider[1];
        this.filterTasks(temp);
    };

    changeDepartureTime = (value, which) => {
        let temp = this.props.filters.slice();
        if(which==='out') {
            temp[3].out = value;
        } else {
            temp[3].in = value;
        }
        this.props.appActions.setFilters(temp);
    }

    departureFilter = (value, which) => {
        let temp = this.props.filters.slice();
        if(which==='out') {
            temp[3].out = value;
        } else {
            temp[3].in = value;
        }
        this.filterTasks(temp);
    }

    changeDuration = (value, which) => {
        let temp = this.props.filters.slice();
        if(which==='out') {
            temp[5].totalTimeOut = value;
        } else {
            temp[5].totalTimeIn = value;
        }
        this.props.appActions.setFilters(temp);
    }

    durationFilter = (value, which) => {
        let temp = this.props.filters.slice();
        if(which==='out') {
            temp[5].totalTimeOut = value;
        } else {
            temp[5].totalTimeIn = value;
        }
        this.filterTasks(temp);
    }

    airlinesFilter = (value) => {
        let temp = this.props.filters.slice();
        if(!temp[6].airlines.includes(value)) {
            temp[6].airlines.push(value);
        } else {
            temp[6].airlines.splice(temp[6].airlines.indexOf(value),1);
        }
        this.filterTasks(temp);
    }

    renderTimeDepartureRange(type) {
        return (
            <Range
                min={0}
                max={24}
                step={3}
                dots={true}
                value={[this.props.filters[3][type][0],this.props.filters[3][type][1]]}
                allowCross={false}
                onChange={value => this.changeDepartureTime(value, type)}
                onAfterChange={value => this.departureFilter(value, type)}
            />
        )
    }

    // setFilters = (absMaxPrice, absMinPrice, filters) => {
    //     let filtersTemp = [
    //         { msg: '' },
    //         { stops: -2 },
    //         { price: [absMinPrice, absMaxPrice], priceSlider: [absMinPrice, absMaxPrice] },
    //         { out: [0, 24], in: [0, 24] },
    //         { outLand: [0, 24], inLand: [0, 24] },
    //         filters[5],
    //         filters[6],
    //         filters[7]
    //     ];
    //     this.filterTasks(filtersTemp);
    // }

    // componentWillReceiveProps(nextProps) {
    //     if(nextProps.absMaxPrice!==this.props.absMaxPrice || 
    //         nextProps.absMinPrice!==this.props.absMinPrice || 
    //         nextProps.filters[5].ttomax!==this.props.filters[5].ttomax ||
    //         nextProps.filters[6].airlinesConst.length!==this.props.filters[6].airlinesConst.length) {
    //         this.setFilters(nextProps.absMaxPrice, nextProps.absMinPrice, nextProps.filters[5], nextProps.filters[6], this.props.filters[7]);
    //     }
    // }

    // componentDidMount() {
    //     setTimeout(()=>this.setState({refresh: true}),1000);
    // }

    render() {
        const { t } = this.props;
        let stops = [
            {label: t('TIP_FLIGHTS_NONSTOP'), value: 0 },
            {label: t('TIP_FLIGHTS_WITH_ONE_STOP'), value: 1 },
            {label: t('TIP_FLIGHTS_WITH_TWO_STOPS'), value: 2 }
        ];
        return(
            <Col xs={12} className="padding-tb5 text-left" key={this.props.loading}>
                {this.props.filters &&
                <Form>
                    {this.props.filters[0].msg==='strict' &&
                        <Alert variant="danger">
                            {t('MSG_TOO_STRICT')}
                        </Alert>
                    }
                    <hr className="margin-t0"/>
                    <h5 className="text-blue">{t('STOPS')}</h5>
                    <Form.Group as={Row} className="padding0 margin-lr-0 margin-b10" >
                        {stops.map((item,i) => 
                            <Form.Check key={i} checked={this.props.filters[1].stops[i]} custom name="flightType" label={item.label} id={"checkStops"+i} aria-label={item.label} className="margin-r10 width-100 margin-b10" value={item.value} onChange={(e)=>this.stopsFilter(e.target.value)}/>
                        )}
                    </Form.Group>
                    <hr/>
                    <h5 className="margin-t20 text-blue">{t('HOTEL_SERACH_SORT_PRICE')}</h5>
                    <Col className="float-left text-light-gray padding-lr0">
                        {this.props.filters[2].priceSlider[0].toLocaleString(this.props.lang.locale.replace('_','-')) }{' - '}
                        {this.props.filters[2].priceSlider[1].toLocaleString(this.props.lang.locale.replace('_','-')) }{' '}
                        {this.props.flights.alt_inf[0].currency}
                    </Col>
                    <div className="clearfix margin-b10"/>
                    <Form.Group as={Row} className="padding0 margin-lr-0 margin-b20" >
                        <Col xs={12} key={this.props.absMaxPrice}>
                            <Range
                                min={this.props.absMinPrice}
                                max={this.props.absMaxPrice}
                                value={[this.props.filters[2].priceSlider[0],this.props.filters[2].priceSlider[1]]}
                                allowCross={false}
                                onChange={value => this.changePrice(value)}
                                onAfterChange={value => this.priceFilter(value)}
                            />
                        </Col>
                    </Form.Group>
                    {!this.props.flights.alt_inf[0].hasOwnProperty('flight3') &&
                    <div>
                        <hr/>
                        <h5 className="margin-t20 text-blue">{t('TITLE_FILTER_DEPARTURE')}</h5>
                            <div className="margin-t20 text-lowercase">{t('SEARCH_DEPARTURE')}</div>
                            <Col className="float-left text-light-gray padding-lr0">
                                <span>{this.props.filters[3].out[0]}{':00'}{' - '}{this.props.filters[3].out[1]}{':00'}</span>
                            </Col>
                        <div className="clearfix margin-b10"/>
                        <Form.Group as={Row} className="padding0 margin-lr-0 margin-b10" >
                            <Col xs={12}>
                                {this.renderTimeDepartureRange('out')}
                            </Col>
                        </Form.Group>
                        {this.props.flights.hasOwnProperty('return') &&
                        typeof this.props.flights.return==='string' &&
                        <div>
                            <div className="margin-t20 text-lowercase">{t('INBOUND')}</div>
                            <Col className="float-left text-light-gray padding-lr0">
                                <span>{this.props.filters[3].in[0]}{':00'}{' - '}{this.props.filters[3].in[1]}{':00'}</span>
                            </Col>
                            <div className="clearfix margin-b10"/>
                            <Form.Group as={Row} className="padding0 margin-lr-0 margin-b10" >
                                <Col xs={12}>
                                    {this.renderTimeDepartureRange('in')}
                                </Col>
                            </Form.Group>
                        </div>}
                        <hr/>
                        <h5 className="margin-t20 text-blue">{t('TITLE_FILTER_FLIGHTS_DURATION')}</h5>
                        <div>
                            <div className="margin-t20 text-lowercase">{t('SEARCH_DEPARTURE')}</div>
                            <Col className="float-left text-light-gray padding-lr0">
                                <span>{Math.floor(this.props.filters[5].ttomin/60)}{t('DURATION_H')}{this.props.filters[5].ttomin%60}{t('DURATION_M') + ' - '}{Math.floor(this.props.filters[5].totalTimeOut/60)}{t('DURATION_H')}{this.props.filters[5].totalTimeOut%60}{t('DURATION_M')}</span>
                            </Col>
                            <div className="clearfix margin-b10"/>
                            <Form.Group as={Row} className="padding0 margin-lr-0 margin-b10" >
                                <Col xs={12}>
                                    <Slider 
                                        min={this.props.filters[5].ttomin}
                                        max={this.props.filters[5].ttomax}
                                        value={this.props.filters[5].totalTimeOut}
                                        onChange={(value)=>{this.changeDuration(value,'out')}} 
                                        onAfterChange={(value) => {this.durationFilter(value,'out')}} />
                                </Col>
                            </Form.Group>
                        </div>
                        {this.props.flights.hasOwnProperty('return') &&
                        typeof this.props.flights.return==='string' &&
                        <div>
                            <div className="margin-t20 text-lowercase">{t('INBOUND')}</div>
                            <Col className="float-left text-light-gray padding-lr0">
                                <span>{Math.floor(this.props.filters[5].ttimin/60)}{t('DURATION_H')}{this.props.filters[5].ttimin%60}{t('DURATION_M') + ' - '}{Math.floor(this.props.filters[5].totalTimeIn/60)}{t('DURATION_H')}{this.props.filters[5].totalTimeIn%60}{t('DURATION_M')}</span>
                            </Col>
                            <div className="clearfix margin-b10"/>
                            <Form.Group as={Row} className="padding0 margin-lr-0 margin-b10" >
                                <Col xs={12}>
                                    <Slider 
                                        min={this.props.filters[5].ttimin}
                                        max={this.props.filters[5].ttimax}
                                        value={this.props.filters[5].totalTimeIn}
                                        onChange={(value)=>{this.changeDuration(value,'in')}} 
                                        onAfterChange={(value) => {this.durationFilter(value,'in')}} />
                                </Col>
                            </Form.Group>
                        </div>
                        }
                    </div>
                    }
                    {this.props.filters[6].airlinesConst &&
                    this.props.filters[6].airlines && <div>
                        <hr/>
                        <div className="clearfix margin-b10 margin-t20"/>
                        <Form.Group as={Row} className="padding0 margin-lr-0 margin-b10" >
                            <Col xs={12} className="padding0">
                                {this.props.filters[6].airlinesConst.map((item,i) =>
                                    <div key={i} className="margin-b10">
                                        <Form.Check custom 
                                            type="checkbox"
                                            checked={this.props.filters[6].airlines.includes(item)}
                                            onChange={() => this.airlinesFilter(item)}
                                            label={
                                                <div>
                                                    <Image 
                                                        src={typeof item==="string"?'/images/airlogos/'+item.toLowerCase()+'.gif':'/images/airlogos/_noimg.gif'}
                                                        onError={e => {e.target.src = '/images/airlogos/_noimg.gif'}}
                                                        className="float-left margin-r10"
                                                        width="24"
                                                    />
                                                    <span className="font14">{this.props.airlines[item]}</span>
                                                </div>
                                            } 
                                            id={'airlines'+i} />
                                    </div>
                                )}
                            </Col>
                        </Form.Group>
                    </div>}
                </Form>}
            </Col>
        )
    }
}

export default Filters;