import React, { Component } from 'react';
import axios from 'axios';
import Routes from './Routes';
import { withRouter } from 'react-router-dom';

class ContentAdvertising extends Component {
      constructor(props) {
        super(props);
        this.state = {
            pageContent: ''
        }
      }

    componentWillMount() {
        axios
            .get("/pages/advertising.html")
            .catch(err => {})
            .then(response => {
                console.log(response.data)
                if(response.status===200 && response.statusText==="OK") {
                    this.setState({ pageContent: response.data });
                }
            })
    }
    render() {
        return (
            <div>
                <Routes {...this.props} />
                {this.state.pageContent!=='' &&
                    <div dangerouslySetInnerHTML={{__html: this.state.pageContent}}></div>
                }
            </div>
        )
    }
}

export default withRouter(ContentAdvertising);