import React, { Component, Suspense } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, compose } from 'redux';
import * as appActions from './actions/appActions';
import moment from 'moment';
import 'react-dates/initialize';
import './App.css';
import './styles/bootstrap/bootstrap.min.css';
import 'react-dates/lib/css/_datepicker.css';
import './styles/loader.css';
import './styles/header.css';
import './styles/style.css';
import RenderRoutes from './components/RenderRoutes';
import { withTranslation } from 'react-i18next';
// import UserInfo from './components/UserInfo';
import { DEFAULT_NUM_PEOPLE } from './common/const';

const AppWithTranslation = withTranslation(['lang', 'countries'])(RenderRoutes);

class App extends Component {

  componentWillMount() {
    if(moment.duration(moment().diff(moment(this.props.timeStamp))).asHours()>30 ||
      moment(this.props.selDates[0])<moment()) {
      this.props.appActions.Reset();
    }
    if(!this.props.numPeople) this.props.appActions.setNumPeople(DEFAULT_NUM_PEOPLE);
    if(this.props.loading!=='none') this.props.appActions.loading('none');
    if(this.props.routes!=='none') this.props.appActions.setRouteInfo('none');
  }

  render() {
    return (
      <div className="App">
        <Suspense fallback=" ">
          <AppWithTranslation {...this.props} />
        </Suspense>
      </div>
    );
  }
}


function mapStateToProps(state) {
  return {
    flights: state.resRed.flights,
    filteredFlights: state.resRed.filteredFlights,
    selectedFlight: state.resRed.selectedFlight,
    acList: state.resRed.acList,
    flightsSearchInfo: state.resRed.flightsSearchInfo,
    filters: state.resRed.filters,
    mobFilters: state.resRed.mobFilters,
    absMaxPrice: state.resRed.absMaxPrice,
    absMinPrice: state.resRed.absMinPrice,
    page: state.resRed.page,
    airlines: state.resRed.airlines,
    lang: state.locRed.lang,
    curr: state.locRed.curr,
    gMapLoaded: state.appRed.gMapLoaded,
    routes: state.appRed.routes,
    loading: state.appRed.loading,
    selDest: state.appRed.selDest,
    selDates: state.appRed.selDates,
    numPeople: state.appRed.numPeople,
    valueDest: state.appRed.valueDest,
    tripType: state.appRed.tripType,
    alert: state.appRed.alert,
    timeStamp: state.appRed.timeStamp,
    sbFocus: state.barRed.sbFocus,
    sbWhere: state.barRed.sbWhere,
    sbIndex: state.barRed.sbIndex,
    sbModify: state.barRed.sbModify,
    peopleInfo: state.formRed.peopleInfo,
    validation: state.formRed.validation,
    additionalServices: state.formRed.additionalServices,
    bookForm: state.bookFormRed,
    flightKey: state.flightAddRed.flightKey,
    bagInfo: state.flightAddRed.bagInfo,
    infoMsg: state.infoRed.infoMsg
  };
}

function mapDispatchToProps(dispatch) {
  return {
    appActions: bindActionCreators(appActions, dispatch)
  };
}

export default compose(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )
)(App);
