import React from "react"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import SupportLevel from "./SupportLevel"
import { GOLD, STANDARD, NONE, PROTECTION, NO_PROTECTION } from "../../actions/allActions"

// this a global store throughout components for additional services fee and total
var plans = {
	total: {
		cancel: 0,
		protection: 0
	},
	gold: 0,
	standard: 0,
	missed: 0
}

function update(state, action, plan, field) {
	const input = state
		.filter(item => !Array.isArray(item)) //cutting out the first array that contains vals
		.map(item => Object.assign({}, item)) //keeping function pure
	const index = input.findIndex(item => Object.keys(item)[0] === action.payload)
	const targetUser = input.splice(index, 1)[0]
	targetUser[action.payload][field].value = plan //an object
	input.splice(index, 0, targetUser) //putting it back where it was on array
	let vals = []
	let cancelTotal = 0
	let protectionTotal = 0
	input.forEach((item, i) => {
		const cancel = item[Object.keys(item)[0]].cancel.value
		if (cancel === GOLD) {
			cancelTotal += parseFloat(plans.gold)
			vals.push(GOLD)
		} else if (cancel === STANDARD) {
			cancelTotal += parseFloat(plans.standard)
			vals.push(STANDARD)
		} else if (cancel === NONE) {
			vals.push(NONE)
		}
		const protection = item[Object.keys(item)[0]].protection.value
		if (protection === PROTECTION) { 
			protectionTotal += parseFloat(plans.missed)
			vals.push(PROTECTION)
		} else if (protection === NO_PROTECTION) {
			vals.push(NO_PROTECTION)
		}
	})

	plans.total.cancel = cancelTotal
	plans.total.protection = protectionTotal
	return [vals, ...input]
}

const reducer = (state, action) => {
	let target
	switch (action.type) {
		case GOLD:
			target = state.filter((item, i) => {
				if (i !== 0) {
					if (Object.keys(item)[0] === action.payload) {
						return item
					}
				}
			})[0]

			return target[action.payload].cancel.value === GOLD
				? update(state, action, NONE, "cancel")
				: update(state, action, GOLD, "cancel")
		case STANDARD:
			target = state.filter((item, i) => {
				if (i !== 0) {
					if (Object.keys(item)[0] === action.payload) {
						return item
					}
				}
			})[0]
			return target[action.payload].cancel.value === STANDARD
				? update(state, action, NONE, "cancel")
				: update(state, action, STANDARD, "cancel")
		case NONE:
			return update(state, action, NONE, "cancel")
		case PROTECTION:
			target = state.filter((item, i) => {
				if (i !== 0) {
					if (Object.keys(item)[0] === action.payload) {
						return item
					}
				}
			})[0]
			return target[action.payload].protection.value === PROTECTION
				? update(state, action, NO_PROTECTION, "protection")
				: update(state, action, PROTECTION, "protection")

		case NO_PROTECTION:
			return update(state, action, NO_PROTECTION, "protection")
		case "INIT": //initialize in useEffect()
			return [[NONE, NO_PROTECTION], ...action.payload]
		default:
			return state
	}
}

function AdditionalServices(props) {
	const { activeTabProps, bookForm } = props
	const { currency } = props.selectedFlight
	const { gold, standard, missed } = props.selectedFlight.data2.cancel_travelwaiver
	Object.assign(plans, { gold, standard, missed })

	const [show, setShow] = React.useState("disappear")
	const [showS, setShowS] = React.useState({ cancelGold: "hide", cancelStandard: "hide", protection: "hide" })
	const [showProtection, setShowProtection] = React.useState("disappear")

// state is an array: [ [an array of selected services, like: "GOLD", "PROTECTION"] ,
// {f_0_0: {}}, {f_0_1: {}} ]
	const [state, dispatch] = React.useReducer(reducer, [])

	const setForAll = type => {
		
		state.forEach((item, i) => {
			if (i !== 0) { // the first element is an array of selected options , skip that.
				dispatch({ type: type, payload: Object.keys(item)[0] })
			}
		})
	}

	React.useEffect(() => {
		//cutting out the bookForm properties needed for this component
		const persons = Object.keys(bookForm)
			.filter(item => /^f_/.test(item))
			.map(item => {
				const obj = {}
				obj[item] = bookForm[item]
				return obj
			})
		dispatch({ type: "INIT", payload: persons })
	}, [])

	React.useEffect(() => {
		//when loads for the first time, set all passengers on default options: free
		setForAll(NONE)
		setForAll(NO_PROTECTION)

		//!!! temporary, remove after tabs are done!!!!!
		props.appActions.setPersonalInput(bookForm, "stage", 2)
	}, [])

	React.useEffect(() => {
		//update the total price over button, on each selection of services changed
		props.appActions.setPersonalInput(bookForm, "services", plans.total)
	}, [plans.total.cancel, plans.total.protection])

	return (
		<div
			id="additional-services-tab"
			className={activeTabProps === 2 ? null : "disappear"}
		>
			<div className="passengers">
				<h2 className="text-left Roboto">Flight Cancellation:</h2>
				<Row onClick={() => setShow("")} className="cancel">
					<Col
						id="gold"
						className="services"
						onClick={e => {
							setForAll(GOLD)
							setShowS(pre => ({ ...pre, cancelGold: "" }))
						}}
					>
						<div id="triangle-topright">
							<p>Best Value!</p>
						</div>
						<div className="header">
							<h1>
								<span className="checkbox">
									{state[0] && state[0].includes(GOLD) ? <span>&#10004;</span> : null}
								</span>
								Gold
							</h1>
							<p>You can cancel for whatever reason - no questions asked!</p>
							<p>
								<i>{`${gold} ${currency}`} per passenger</i>
							</p>
						</div>
						<div className={"persons " + show + " " + showS.cancelGold}>
							<EachPassenger
								field="cancel"
								state={state}
								dispatch={dispatch}
								type={GOLD}
								cost={{ fee: gold, currency }}
							/>
						</div>
					</Col>
					<Col
						id="standard"
						className="services"
						onClick={() => {
							setForAll(STANDARD)
							setShowS(pre => ({ ...pre, cancelStandard: "" }))
						}}
					>
						<div className="header">
							<h1>
								<span className="checkbox">
									{state[0] && state[0].includes(STANDARD)  ? (
										<span>&#10004;</span>
									) : null}
								</span>
								Standard
							</h1>
							<p>You may cancel if you are ill or due to death in your family.</p>
							<p>
								<i>{`${standard} ${currency}`} per passenger</i>
							</p>
						</div>
						<div className={"persons " + show + " " + showS.cancelStandard}>
							<EachPassenger
								field="cancel"
								state={state}
								dispatch={dispatch}
								type={STANDARD}
								cost={{ fee: standard, currency }}
							/>
						</div>
					</Col>
					<Col
						id="none"
						className="services"
						onClick={() => {
							setForAll(NONE)
						}}
					>
						<div className="header">
							<h1>
								{" "}
								<span className="checkbox">
									{state[0] && state[0].includes(NONE) ? <span>&#10004;</span> : null}
								</span>
								No, thank you
							</h1>
							<p style={{ visibility: "hidden" }}>
								Lorem ipsum dolor sit amet, Lorem ipsum Lorem ipsum
							</p>
							<p style={{ visibility: "hidden" }}>
								<i>{`0 per passenger per`}</i>
							</p>
						</div>
						<div className={"persons " + show + " hide"}>
							<EachPassenger
								field="cancel"
								state={state}
								dispatch={dispatch}
								type={NONE}
								cost={{ fee: 0, currency }}
							/>
						</div>
					</Col>
				</Row>
			</div>
			<div className="passengers">
				<h2 className="text-left Roboto">Missed Flight Protection:</h2>
				<Row onClick={() => setShowProtection("")} className="protection-row">
					<Col id="yes" className="protection" onClick={() => {setForAll(PROTECTION); setShowS(pre => ({ ...pre, protection: "" }))}}>
						<div className="header">
							<h1>
							<span className="checkbox">
									{state[0] && state[0].includes(PROTECTION) ? <span>&#10004;</span> : null}
								</span>
								Yes!</h1>
							<p> If you miss your connecting flight, just book the next one.</p>
							<p>
								<i>{`${missed} ${currency}`} per passenger</i>
							</p>
						</div>
						<div className={"persons " + showProtection + " " + showS.protection}>
							<EachPassenger
								field="protection"
								state={state}
								dispatch={dispatch}
								type={PROTECTION}
								cost={{ fee: missed, currency }}
							/>
						</div>
					</Col>
					<Col id="none" className="protection" onClick={() => setForAll(NO_PROTECTION)}>
						<div className="header">
							<h1><span className="checkbox">
									{state[0] && state[0].includes(NO_PROTECTION) ? <span>&#10004;</span> : null}
								</span>
								No</h1>
							<p style={{ visibility: "hidden" }}>
								fliller text fliller text fliller text fliller text fliller text fliller
								{" "}
							</p>
							<p style={{ visibility: "hidden" }}>
								<i> 0 per passenger per </i>
							</p>
						</div>
						<div className={"persons " + showProtection + " hide "}>
							<EachPassenger
								field="protection"
								state={state}
								dispatch={dispatch}
								type={NO_PROTECTION}
								cost={{ fee: 0, currency }}
							/>
						</div>
					</Col>
				</Row>
			</div>
		{/*	<SupportLevel />*/}
		</div>
	)
}

function EachPassenger(props) {
	const { state, dispatch, type, field, cost } = props
	//console.log(props)
	return (
		<div>
			{state.map((item, i) => {
				if (i !== 0) {
					// the first element of state, is an array that hold the values selected
					const user = Object.keys(item)[0]
					return (
						<p
							key={i}
							onClick={e => {
								e.stopPropagation()
								dispatch({ type: type, payload: user, fee: cost.fee })
							}}
						>
							{item[user][field].value === type ? (
								<span>&#9745;</span>
							) : (
								<span>&#9744;</span>
							)}
							{` ${item[user].firstName.value} ${item[user].lastName.value}`}
						</p>
					)
				}
			})}
		</div>
	)
}

export default AdditionalServices