import React, { Component } from 'react';
import NumberPeople from './NumberPeople';
import axios from 'axios';
import moment from 'moment';
import 'moment/min/locales.min';
import OutsideClickHandler from 'react-outside-click-handler';
// import InfiniteCalendar from 'react-infinite-calendar';
import { DateRangePicker, SingleDatePicker, isInclusivelyAfterDay } from 'react-dates';
// import Calendar from './Calendar';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import SweetAlert from 'react-bootstrap-sweetalert';
// import InputCity from './InputCity';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Dropdown from 'react-bootstrap/Dropdown';
import Modal from 'react-bootstrap/Modal';
import { scroller, Element } from 'react-scroll';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch, faTimes, faCalendarAlt, faPlaneDeparture, faPlaneArrival, faPlusCircle, faPlane, faBuilding } from '@fortawesome/free-solid-svg-icons';

import { CITY_AC, FLIGHT_NUM_LIMIT } from '../common/const';

class MenuToggle extends Component {
    constructor(props) {
        super(props);
        this.cityInput = React.createRef();
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(e) {
        e.preventDefault();
        if(this.props.sbFocus==='none') return;
        let tempVal = this.props.valueDest.slice();
        tempVal[this.props.index][this.props.where] = e.target.value;
        this.props.appActions.setValDest(tempVal);
        if(e.target.value.length>1)
            axios
                .get(CITY_AC+e.target.value+'&hl='+this.props.lang.value)
                .catch(err => {})
                .then(response => {
                    if(response) {
                        this.props.appActions.setAcList(response.data);
                    }
                });
        else if(e.target.value.length===0) this.props.appActions.setAcList([]);
    }

    render() {
        const { t } = this.props;
        return (
            <Form.Group>
                <Form.Control
                    className={this.props.redText && "text-danger"}
                    type="text"
                    placeholder={this.props.where==='from'? t('FROM'):t('TO')}
                    aria-label={this.props.where}
                    value={this.props.valueDest[this.props.index][this.props.where]}
                    onChange={(e) => this.handleChange(e)}
                />
            </Form.Group>
        );
    }
}

// main searchbar component
class SearchBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            errorCities: [],
            focusDates: [false],
            showModalCity: false,
            showModalDate: false
        }
    }

    selectCity = (city,index,where) => {
        let tempSel = this.props.selDest.slice();
        let tempVal = this.props.valueDest.slice();
        tempSel[index][where]=city;
        tempVal[index][where]=city.value+' - '+city.name+' ('+city.cc+')';
        if(where==='to' && tempSel[index+1] && ((tempSel[index+1]['from']!==null && city.value!==tempSel[index+1]['to'].value) || tempSel[index+1]['from']===null)) {
            tempSel[index+1]['from']=city;
            tempVal[index+1]['from']=city.value+' - '+city.name+' ('+city.cc+')';
        }
        if(this.props.tripType==='round' && where==='from' && tempSel[index+1]) {
            tempSel[index+1]['to']=city;
            tempVal[index+1]['to']=city.value+' - '+city.name+' ('+city.cc+')';
        }
        this.props.appActions.setSelDest(tempSel);
        this.props.appActions.setValDest(tempVal);
        this.props.appActions.setAcList([]);
    }

    focusThis = (index,where) => {
        if(this.state.errorCities.length>0) this.setState( {errorCities: []} );
        scroller.scrollTo(where+index, {
            duration: 1000,
            delay: 100,
            smooth: true
        });
        this.props.appActions.setSbFocus(where+index);
        let tempSel = this.props.selDest.slice();
        let tempVal = this.props.valueDest.slice();
        for(let i=0;i<tempSel.length;i++) {
            if(index===i) {
                tempSel[index][where] = null;
                tempVal[index][where] = '';
                this.props.appActions.setSelDest(tempSel);
                this.props.appActions.setValDest(tempVal);
                setTimeout(()=>this.props.appActions.setAcList([]), 500);
            } 
        }
    }

    blurThis = (where,i) => {
        if(this.props.sbFocus===where+i) {
            setTimeout(()=>this.props.appActions.setSbFocus('none'), 500);
            setTimeout(()=>this.props.appActions.setAcList([]), 500);
        }
    }

    handleDateChange = (date,index) => {
        if(index===-1) {
            let temp = date.slice();
            if((date[1]!==null && moment(date[0])>moment(date[1])) || date[1]===null) {
                temp[1]=moment(date[0]).add(1,'days');
            }
            this.props.appActions.setDate(temp);
        } else {
            let temp = this.props.selDates.slice();
            temp[index] = date;
            for(let i=index+1;i<temp.length;i++){
                if(i<temp.length && moment(temp[i])<moment(temp[i-1])) temp[i]=temp[i-1];
            }
            this.props.appActions.setDate(temp);
        }
    }

    manageFlights =(action, index) => {
        let tempDest = this.props.selDest.slice();
        let tempDates = this.props.selDates.slice();
        let tempVal = this.props.valueDest.slice();
        let tempFocDates = this.state.focusDates.slice();
        if(action==='add') {
            tempDest.push({from: this.props.selDest[this.props.selDest.length-1].to, to: null});
            if(tempDest.length>2) tempDates.push(moment(this.props.selDates[this.props.selDates.length-1]).add(1,'days'));
            tempVal.push({from: this.props.valueDest[this.props.valueDest.length-1].to, to: ''});
            tempFocDates.push(null);
        } else if (this.props.selDest.length>1) {
            if(this.props.selDest.length>2) {
                tempDest.splice(index,1);
                tempDates.splice(index,1);
                tempVal.splice(index,1);
                tempFocDates.splice(index,1);
            }
            if(this.props.selDest.length === 2) {
                this.props.appActions.setTripType("round");
            }
        }
        this.props.appActions.setSelDest(tempDest);
        this.props.appActions.setDate(tempDates);
        this.props.appActions.setValDest(tempVal);
        this.setState({focusDates: tempFocDates});
    }

    changeTripType = (trip) => {
        this.props.appActions.setTripType(trip);
        switch(trip) {
            case "round":
                console.log(this.props.selDates)
                if(moment(this.props.selDates[1]).diff(moment(this.props.selDates[0]))<0){
                    let tempDates = this.props.selDates.slice();
                    tempDates[1] = moment(tempDates[0]).add(1,'days');
                    this.props.appActions.setDate(tempDates);
                }
                break;
            case "one":
                break;
            case "multi":
                if(this.props.selDest.length===1) this.manageFlights('add',null);
                break;
            default: break;
        }
    }

    renderModalCity = (where, i) => {
        return (
            <Form.Group controlId="formBasicEmail" onFocus={()=>this.focusThis(i,where)}>
                <Form.Control as={MenuToggle} {...this.props} where={where} index={i} />
                <div key={this.props.acList.length} className={this.props.acList.length>0?"d-block no-transition":"d-none"}>
                    {this.props.acList.map((item,j) => (
                        <div key={j} onClick={()=>{this.selectCity(item,i,where);this.toggleCityChooser(null,null)}} className="city-select cursor-pointer">{item.name + ' - ' +item.value}</div>
                    ))}
                </div>
            </Form.Group>
        )
    }

    renderDropdownCity = (where,i) => {
        return (
            <OutsideClickHandler
                onOutsideClick={()=>this.blurThis(where,i)}
            >
                <Col className="d-block d-sm-none"><Element name={where+i} /></Col>
                <Dropdown 
                    key={where+i} 
                    id={where+i} 
                    show={this.props.sbFocus===where+i} 
                    onFocus={()=>this.focusThis(i,where)}
                    // onBlur={()=>this.blurThis()}
                >
                    <Dropdown.Toggle as={MenuToggle} {...this.props} redText={this.state.errorCities.includes(i)} where={where} index={i}>
                    </Dropdown.Toggle>
                    <Dropdown.Menu key={this.props.acList.length} className={this.props.acList.length>0 && this.props.sbFocus!=='none'? (this.props.tripType!=='multi'?"d-block menu1":"d-block menu1 menu1-multi"):"d-none menu1"} >
                        {this.props.acList.map((item,j) => (
                            <Dropdown.Item key={j} onClick={()=>this.selectCity(item,i,where)} className="margin-t4 margin-b5 font14">
                                <div className="menu1-ddown-icon text-blue margin-r10 margin-l-10 ddown-icon float-left">
                                    {item.type==='city'?<FontAwesomeIcon icon={faBuilding} />:
                                    <FontAwesomeIcon icon={faPlane} />}
                                </div>
                                <div className="menu1-ddown-text font14">{item.name + ' (' + item.cc + ') - ' +item.value}</div>
                            </Dropdown.Item>
                        ))}
                    </Dropdown.Menu>
                </Dropdown>
            </OutsideClickHandler>
        )
    }

    toggleCalendar = () => {
        this.setState({
            showModalDate: !this.state.showModalDate
        })
    }

    toggleCityChooser = (where, i) => {
        this.props.appActions.setSbWhere(where);
        this.props.appActions.setSbIndex(i);
        this.setState({
            showModalCity: !this.state.showModalCity
        });
    }

    submitSearch = (e) => {
        e.preventDefault();
        let url = '';
        let urlDestDate = '';
        let urlPeople = '';
        let tempDest = this.props.selDest.slice();
        let tempDestVal = this.props.valueDest.slice();
        let checkDestLength=
            this.props.tripType==='round'?
            2:(this.props.tripType==='one'?
                1:this.props.selDest.length);
        for(let i=0;i<checkDestLength;i++) {
            if(this.props.selDest[i].from===null || this.props.selDest[i].to===null) {
                this.props.appActions.setAlert('sb-alert');
                return;
            }
        }
        let errorCities = [];
        switch(this.props.tripType) {
            case "round":
                if(this.props.selDest[0]['from'].value === this.props.selDest[0]['to'].value) errorCities = [0];  
                urlDestDate = '&T1=' +
                    this.props.selDest[0]['from'].value +
                    '&T2=' +
                    this.props.selDest[0]['to'].value +
                    '&T3=' +
                    this.props.selDest[0]['to'].value +
                    '&T4=' +
                    this.props.selDest[0]['from'].value +
                    '&outdate1=' +
                    moment(this.props.selDates[0]).format('YYYY-MM-DD') + 
                    '&outdate2=' +
                    moment(this.props.selDates[1]).format('YYYY-MM-DD');
                tempDest = this.props.selDest.slice(0,2);
                tempDestVal = this.props.valueDest.slice(0,2);
                tempDest[1].from=tempDest[0].to;
                tempDest[1].to=tempDest[0].from;
                tempDestVal[1].from=tempDestVal[0].to;
                tempDestVal[1].to=tempDestVal[0].from; 
                break;
            case "one":
                if(this.props.selDest[0]['from'].value === this.props.selDest[0]['to'].value) errorCities = [0];  
                tempDest = this.props.selDest.slice(0,1);
                tempDestVal = this.props.valueDest.slice(0,1);
                urlDestDate = '&T1=' +
                    this.props.selDest[0]['from'].value +
                    '&T2=' +
                    this.props.selDest[0]['to'].value +
                    '&outdate1=' +
                    moment(this.props.selDates[0]).format('YYYY-MM-DD')
                break;
            case "multi":
                for(let i=0;i<this.props.selDest.length;i++) {
                    if(this.props.selDest[i]['from'].value === this.props.selDest[i]['to'].value) errorCities.push(i);
                    urlDestDate=urlDestDate+'&T'+(i*2+1)+'='+this.props.selDest[i]['from'].value+'&T'+(i*2+2)+'='+this.props.selDest[i]['to'].value+'&outdate'+(i+1)+'='+moment(this.props.selDates[i]).format('YYYY-MM-DD');
                }
                break;
            default:
                break;
        }
        urlPeople = 
            '&D1='
            +this.props.numPeople.adult +
            '&D2=' +
            this.props.numPeople.child +
            '&D3=' +
            this.props.numPeople.infant +
            '&D4=' +
            this.props.numPeople.senior +
            '&D5=' +
            this.props.numPeople.youth;
        url = 
            urlDestDate +
            urlPeople + 
            '&trip=' +
            (this.props.tripType==='one'?'one':(urlDestDate.includes("&T5=")?'round&multi=1':'round')) +
            FLIGHT_NUM_LIMIT +
            '&curr=' +
            this.props.curr.cc;
        let tempFlightsSearchInfo = { 
            url: url,
            curr: this.props.curr,
            lang: this.props.lang,
            dest: tempDest,
            destVal: tempDestVal,
            dates: this.props.selDates,
            people: this.props.numPeople,
            type: this.props.tripType
        }
        if(errorCities.length===0) this.props.appActions.fetchFlightsData(url, tempFlightsSearchInfo)
        else {
            this.setState( { errorCities: errorCities } );
            this.props.appActions.setAlert('sb-alert');
        }
    }

    componentWillMount() {
        if(this.state.focusDates.length!==this.props.selDest.length) {
            let temp = this.state.focusDates.slice();
            for(let i=0;i<this.props.selDates.length-this.state.focusDates.length;i++) {
                temp.push(null);
            }
            this.setState({focusDates: temp});
        }
    }

    render() {
        const { t } = this.props;
        let tType = [
            { label: t('ROUND_TRIP'), value: 'round' },
            { label: t('ONE_WAY'), value: 'one' },
            { label: t('MULTI_TRIP'), value: 'multi' }
        ];
        let gridDest = {};
        let gridDate = {};
        let gridPeople = {xs: 12, sm: 12, md: 5, lg: 2};
        let gridAddFlight = {xs: 12, sm: 6, md: 3, lg: 3};
        let gridRemove = {xs: 12, sm: 12, md: 1, lg: 1};
        switch(this.props.tripType) {
            case "round":
                gridDest = {xs: 12, sm: 12, md: 10, lg: 6};
                gridDate = {xs: 12, sm: 12, md: 5, lg: 3};
                break;
            case "one":
                gridDest = {xs: 12, sm: 12, md: 10, lg: 6};
                gridDate = {xs: 12, sm: 12, md: 5, lg: 3};
                break;
            case "multi":
                gridDest = {xs: 8, sm: 9, md: 8, lg: 8};
                gridDate = {xs: 4, sm: 3, md: 3, lg: 3};
                gridPeople = {xs: 12, sm: 12, md: {span: 4, offset: 4}, lg: {span: 3, offset: 5}};
                break;
            default:
                break;
        }
        return (
            <div>
            {this.props.alert==='sb-alert' && <SweetAlert title="Check your search inputs!" type="warning" onConfirm={() => this.props.appActions.setAlert('none')} />}
            <Modal show={this.state.showModalCity} onHide={()=>this.toggleCityChooser(null, null)} className="modal-city">
                <Modal.Header closeButton>
                    <Modal.Title>{this.props.sbWhere!==null}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {this.props.sbWhere!==null && this.props.sbIndex!==null && this.renderModalCity(this.props.sbWhere, this.props.sbIndex)}
                </Modal.Body>
            </Modal>
    
            {this.props.selDest && 
            this.props.selDates && 
            this.props.numPeople && 
            this.props.valueDest &&
            this.props.valueDest &&
                <Form>
                    <fieldset>
                        <Form.Group as={Row} key={this.props.tripType} className="padding-lr2 margin-lr-0 padding-tb0 trip-type-check">
                            {tType.map((item,i) => 
                                <Form.Check 
                                    key={i} 
                                    checked={this.props.tripType===item.value} 
                                    onChange={(e)=>this.changeTripType(e.target.value)} 
                                    custom 
                                    name="flightType" 
                                    type="radio" 
                                    label={item.label} 
                                    id={"tripType"+i} 
                                    aria-label={item.label} 
                                    className="margin-r20 padding-tb10" 
                                    value={item.value}
                                />
                            )}
                        </Form.Group>
                    </fieldset>
                    <Row className="padding0 margin0">
                        {this.props.selDest.map((flight,i) =>
                        <div className="width-100" key={i}>
                            {(this.props.tripType==="multi" ||
                            (this.props.type!=="multi" && i===0)) &&
                            <Col xs={12} className="padding0">
                                <Col xs={gridDest.xs} sm={gridDest.sm} md={gridDest.md} lg={gridDest.lg} className="city-input float-left padding0">
                                    <Col xs={12} sm={this.props.tripType==='multi'?12:6} md={6} className="float-left padding-lr2 padding-tb5">
                                        {this.renderDropdownCity('from',i)}
                                        <FontAwesomeIcon icon={faPlaneDeparture} className="plane-icon"/>
                                    </Col>
                                    <Col xs={12} sm={this.props.tripType==='multi'?12:6} md={6} className="float-left padding-lr2 padding-tb5">
                                        {this.renderDropdownCity('to',i)}
                                        <FontAwesomeIcon icon={faPlaneArrival} className="plane-icon"/>
                                    </Col>
                                </Col> 
                                {this.props.tripType==="multi" &&
                                    <Col xs={gridDate.xs} sm={gridDate.sm} md={gridDate.md} lg={gridDate.lg} className="float-left  padding-lr2 padding-tb5">
                                        <SingleDatePicker
                                            date={moment(this.props.selDates[i])} // momentPropTypes.momentObj or null
                                            onDateChange={(date) => this.handleDateChange(date,i)} // PropTypes.func.isRequired
                                            focused={this.state.focusDates[i]} // PropTypes.bool
                                            onFocusChange={() => {
                                                    let temp = this.state.focusDates.slice();
                                                    temp[i] = !temp[i];
                                                    this.setState({ focusDates:  temp })}
                                                } // PropTypes.func.isRequired
                                            hideKeyboardShortcutsPanel={true}
                                            id="datepicker-1" // PropTypes.string.isRequired,
                                            numberOfMonths={1}
                                            isOutsideRange={(day) => isInclusivelyAfterDay(i!==0?moment(this.props.selDates[this.props.selDates.length-2]).subtract(1,'day'):moment().subtract(1,'day'), day)}
                                            withPortal={true}
                                            transitionDuration={100}
                                            displayFormat="MMM D"
                                            readOnly={true}
                                            // noBorder={true}
                                        />
                                    </Col>
                                }
                                {this.props.tripType!=="multi" &&
                                    <Col xs={gridDate.xs} sm={gridDate.sm} md={gridDate.md} lg={gridDate.lg} className="float-left text-center padding-lr2 padding-tb5">
                                        <DateRangePicker
                                            startDate={moment(this.props.selDates[0])} // momentPropTypes.momentObj or null,
                                            startDateId="startD" // PropTypes.string.isRequired,
                                            endDate={this.props.tripType==='round'?moment(this.props.selDates[1]):null} // momentPropTypes.momentObj or null,
                                            endDatePlaceholderText={t('NONE')}
                                            endDateId="endD" // PropTypes.string.isRequired,
                                            onDatesChange={({ startDate, endDate }) => this.handleDateChange([startDate,endDate],-1)}// PropTypes.func.isRequired,
                                            focusedInput={this.state.focusedInput} // PropTypes.oneOf([START_DATE, END_DATE]) or null,
                                            onFocusChange={focusedInput => this.setState({ focusedInput })} // PropTypes.func.isRequired,
                                            transitionDuration={100}
                                            hideKeyboardShortcutsPanel={true}
                                            numberOfMonths={1}
                                            disabled={this.props.tripType==="one"?"endDate":false}
                                            minimumNights={0}
                                            displayFormat="MMM D"
                                            readOnly={true}
                                            withPortal={true}
                                            renderCalendarInfo={()=>{return <div className="text-center margin-b10 text-light-gray">
                                                <hr className="margin-t0 hr-1"/>
                                                {this.state.focusedInput==='startDate'?
                                                <span className="font-weight-bold">
                                                    <FontAwesomeIcon icon={faPlane} className="margin-r10"/>
                                                    <span className="margin-r10 text-lowercase">{t('OUTBOUND')}</span>
                                                    <FontAwesomeIcon icon={faPlane} />
                                                </span>:
                                                <span className="font-weight-bold">
                                                    <FontAwesomeIcon icon={faPlane} className="margin-r10 fa-rotate-180"/>
                                                    <span className="margin-r10 text-lowercase">{t('INBOUND')}</span>
                                                    <FontAwesomeIcon icon={faPlane} className="fa-rotate-180"/>
                                                </span>}</div>}}
                                            // noBorder={true}
                                        />
                                        <FontAwesomeIcon icon={faCalendarAlt} className="calendar-icon"/>
                                    
                                    </Col>
                                }
                                {this.props.tripType==="multi" &&
                                    <Col xs={gridRemove.xs} md={gridRemove.md} className="float-left padding0 margin-t7" onClick={()=>{this.manageFlights('remove',i)}}>
                                        <div className="button-remove cursor-pointer">
                                            <FontAwesomeIcon icon={faTimes} />
                                        </div>
                                    </Col>
                                }
                                {this.props.tripType!=="multi" &&
                                    <Col xs={gridPeople.xs} sm={gridPeople.sm} md={gridPeople.md} lg={gridPeople.lg} className="float-left padding-lr2 padding-tb5">
                                        <NumberPeople {...this.props} />
                                    </Col>
                                }
                                {this.props.tripType!=="multi" &&
                                    <Col lg={1} md={2} className="float-right  padding-lr2 padding-tb5">
                                        <Button variant="primary" className="search-btn  accent has-glow fill" onClick={(e)=>this.submitSearch(e)} >
                                            <FontAwesomeIcon icon={faSearch} />
                                        </Button>
                                    </Col>
                                }
                            </Col>
                            }
                            <div className="clearfix" />
                            {this.props.tripType==="multi" && 
                                <Col xs={12} className="padding-tb5">
                                </Col>
                            }
                        </div>
                        )}
                        {this.props.tripType==="multi" &&
                            <Col xs={gridAddFlight.xs} md={gridAddFlight.md} className="padding-lr2 padding-tb5">
                                {this.props.selDest.length<10 &&
                                    <Button variant="outline-primary" className="float-left width-100" onClick={()=>this.manageFlights('add')}>
                                        <FontAwesomeIcon icon={faPlusCircle} className="margin-r10" />{t('SEARCH_FLIGHT_ADD')}
                                    </Button>
                                }
                            </Col>
                        }
                        {this.props.tripType==="multi" &&
                            <Col xs={gridPeople.xs} sm={gridPeople.sm} md={gridPeople.md} lg={gridPeople.lg} className="padding-lr2 padding-tb5">
                                <NumberPeople {...this.props} />
                            </Col>
                        }
                        {this.props.tripType==="multi" &&
                            <Col xs={12} md={{span: 4, offset: 7 }} lg={{span: 3, offset: 8 }} className="float-right padding-tb5 padding-lr2 margin-t20">
                                <Button className="search-btn accent has-glow fill float-right width-100" onClick={(e)=>this.submitSearch(e)}>
                                    <FontAwesomeIcon icon={faSearch} className="margin-r10" />{t('BUTTON_SEARCH')}
                                </Button>
                            </Col>
                        }
                    </Row>
                    <div className="clearfix"></div>
                </Form>
            }
            </div>
        );
  }
}

export default SearchBar;
