import initialStateResults from './initialStateResults';
import {
    SET_FLIGHTS_DATA,
    FETCH_FLIGHTS_DATA,
    SET_SELECTED_FLIGHT,
    SET_FILTERED_FLIGHTS,
    SET_AC_LIST,
    SET_AIRLINES,
    SET_FLIGHTS_SEARCH_INFO,
    FILTERS,
    PAGES,
    MOB_FILTERS,
    MAX_PRICE,
    MIN_PRICE
} from '../actions/allActions';

export default function resRed(state = initialStateResults, action) {
    switch (action.type) {
        case SET_FLIGHTS_DATA:
            return {
              ...state,
              flights: action.flights,
              filteredFlights: [],
              filters: state.filters
            };
        case SET_FILTERED_FLIGHTS: 
            return {
                ...state,
                filteredFlights: action.filteredFlights
            }
        case FETCH_FLIGHTS_DATA:
            return {
              ...state,
              filteredFlights: [],
              flights: []
            };
        case SET_SELECTED_FLIGHT: 
            return { ...state, selectedFlight: action.selectedFlight };
        case SET_AC_LIST:
            return { ...state, acList: action.acList };
        case SET_FLIGHTS_SEARCH_INFO:
                return { ...state, flightsSearchInfo: action.flightsSearchInfo };
        case SET_AIRLINES:
            return { ...state, airlines: action.airlines };
        case FILTERS:
            return { ...state, filters: action.filters };
        case PAGES:
            return { ...state, page: action.page };
        case MOB_FILTERS:
            return { ...state, mobFilters: action.mobFilters };
        case MAX_PRICE:
            return { ...state, absMaxPrice: action.absMaxPrice };
        case MIN_PRICE:
            return { ...state, absMinPrice: action.absMinPrice };
        default:
            return state;
        }

}