import React, { Component } from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { PEOPLE } from '../common/const';

class FlightPriceDetails extends Component {
    
    render() {
        const { t } = this.props;
        let priceDiff = (parseFloat(this.props.selectedFlight.TOTAL_PRICE) - parseFloat(this.props.selectedFlight.TTL_FAR))/this.props.numPeople;
        return(
            <div>
                {PEOPLE.map((item,i) => (
                    parseInt(this.props.selectedFlight.search[item.which])>0 && 
                    <div key={i} className="padding-tb10 text-light-gray font12">
                        <Row>
                            <Col xs={12} className="text-gray font-weight-bold font14">
                                {t(item.label1).toLowerCase()}
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={7}>{t('BASEFARE_BLOCK_PRICE')}</Col>
                            <Col xs={5}>
                                {parseFloat(this.props.selectedFlight[item.which].BAS_FAR).toLocaleString(this.props.lang.locale.replace('_','-')) }{' '}
                                {this.props.selectedFlight.currency}
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={7}>{t('TAXES_BLOCK_PRICE')}</Col>
                            <Col xs={5}>
                                {(parseFloat(this.props.selectedFlight[item.which].TAX)+priceDiff).toLocaleString(this.props.lang.locale.replace('_','-')) }{' '}
                                {this.props.selectedFlight.currency}
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={7}>{t('TOTAL_BLOCK_PRICE')}</Col>
                            <Col xs={5}>
                                {(parseFloat(this.props.selectedFlight[item.which].TTL_FAR)+priceDiff).toLocaleString(this.props.lang.locale.replace('_','-')) }{' '}
                                {this.props.selectedFlight.currency}
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={7}>{t('NUMBER_BLOCK_PRICE')}</Col>
                            <Col xs={5}>
                                {this.props.selectedFlight.search[item.which]}
                            </Col>
                        </Row>
                    </div>
                ))}
            </div>
        )
    }
}

export default FlightPriceDetails;