/* eslint-disable array-callback-return */
import React, { Component } from 'react';
import Routes from './Routes';
import Header from './Header';
import Flights from './Flights';
import Filters from './Filters';
import SearchInfo from './SearchInfo';
import { withRouter } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import InfoMsg from './InfoMsg';
import queryString from 'query-string';
import moment from 'moment';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFilter } from '@fortawesome/free-solid-svg-icons';
import currencies from '../common/currencies';
import langs from '../common/langs';
import SweetAlert from 'react-bootstrap-sweetalert';
import { SEARCH_PARAMS } from '../common/const';

class ContentSearchFlights extends Component {
    constructor(props) {
        super(props);
        this.state={
            toggleAlert: false,
        }
        this.alertCalled = false;
    }

    newSearch = (reqUrl) => {
        let values = queryString.parse(reqUrl);
        let i=0;
        let tempUrl = '';
        let tempDest = [];
        let tempDestVal = [];
        for (let property in values) {
            if(SEARCH_PARAMS.includes(property))
                tempUrl =
                    tempUrl + "&" + property + "=" + values[property]
        }
        do {
            if(values['T'+(i+1)]) {
                if(i%2===1) {
                    tempDest[Math.floor(i/2)].to.value=values['T'+(i+1)];
                    tempDestVal[Math.floor(i/2)].to=values['T'+(i+1)];
                } else {
                    tempDest.push({from:{},to:{}});
                    tempDestVal.push({from:'',to:''});
                    tempDest[i/2].from.value=values['T'+(i+1)];
                    tempDestVal[i/2].from=values['T'+(i+1)];
                }
            }
            i++;
        } while (values['T'+i]); 
        if(values.trip==='one') {
            tempDest.push({from: {value: values['T2']},to: {value: values['T1']}});
            tempDestVal.push({from: values['T2'],to: values['T1']});
        }
        i=0;
        let tempDates=[];
        do {
            if(values['outdate'+(i+1)]) {
                tempDates.push(moment(values['outdate'+(i+1)],'YYYY-MM-DD'));
            }
            i++;
        } while (values['T'+i]);
        let tempPeople = { 
            adult: parseInt(values.D1), 
            child: parseInt(values.D2), 
            infant: parseInt(values.D3), 
            senior: parseInt(values.D4), 
            youth: parseInt(values.D5)
        }
        this.props.appActions.setDate(tempDates);
        this.props.appActions.setSelDest(tempDest);
        this.props.appActions.setValDest(tempDestVal);
        this.props.appActions.setNumPeople(tempPeople);
        this.props.appActions.setTripType(values.trip);
        
        let xLang = this.props.lang;
        let l = this.props.match.params.lang;
        langs.filter(function(obj) {
            if (obj.value === l) {
            xLang = obj;
            xLang['url'] = obj.value;
            }
        });
        let xCurr = this.props.curr;
        // let c = this.props.match.params.curr;
        let c = values['curr'];
        currencies.filter(function(obj) {
            if (obj.cc === c) {
                xCurr = obj;
                xCurr['url'] = obj.cc;
                tempUrl = tempUrl+'&curr='+xCurr.cc;
            }
            });
        //assign new url value, if changed it will update the currency value
        if(this.props.curr.cc !== xCurr.cc) this.props.appActions.changeCurrency(xCurr);
        let tempFlightsSearchInfo = { 
            url: reqUrl,
            curr: xCurr,
            lang: xLang,
            dest: tempDest,
            destVal: tempDestVal,
            dates: tempDates,
            people: tempPeople,
            type: values.trip
        }
        if(this.alertCalled===false) {
            setTimeout(()=>this.callAlert(),1000);
        }
        this.props.appActions.fetchFlightsData(tempUrl, tempFlightsSearchInfo, true);
    }

    callAlert = () => {
        let difference = moment.duration(moment(this.props.timeStamp).add(15, 'minutes').diff(moment())).asMilliseconds();
        setTimeout(()=>this.setState({ toggleAlert: true }), difference<0?0:difference)
        console.log(this.props.timeStamp,difference)
        this.alertCalled = true;
    }

    removeAlert = () => {
        this.setState({ toggleAlert: false });
        this.alertCalled = false;
        // this.newSearch(decodeURIComponent(this.props.match.params.query));
        this.newSearch(decodeURIComponent(window.location.search));
    }

    componentWillMount() {
        setTimeout(()=>this.callAlert(),1000);
        // let reqUrl = decodeURIComponent(this.props.match.params.query);
        let reqUrl = decodeURIComponent(window.location.search.substr(1));
        if(reqUrl!==this.props.flightsSearchInfo.url) {
            this.newSearch(reqUrl);
        }
    }
    
    render() {
        return(
            <div>
                <Routes {...this.props} />
                <Header {...this.props} page='search'/>
                {this.state.toggleAlert && 
                this.props.loading==='none' &&
                    <SweetAlert 
                        title="Refresh your search!" 
                        onConfirm={()=>this.removeAlert()}>
                            <Image src="/images/icons/icon-alert.jpg" alt="refresh alert" title="refresh alert" className="add-animation-shake" />
                    </SweetAlert>
                }
                <Modal show={this.props.mobFilters} onHide={()=>this.props.appActions.setMobFilters(!this.props.mobFilters)} className="modal-city">
                    <Modal.Body>
                        <div className="padding-lr10 font22 text-blue">Filter by</div>
                        <Filters {...this.props}/>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant='danger' onClick={()=>this.props.appActions.setMobFilters(!this.props.mobFilters)}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Modal>
                <InfoMsg {...this.props} />
                <Container>
                    <SearchInfo {...this.props} />
                    {this.props.flights && 
                    this.props.flights.hasOwnProperty('alt_inf') && 
                    this.props.flights.alt_inf.length>0 ?
                        <Row className="margin-t50">
                            <Col xs={12} className="text-left text-blue">
                                {this.props.filteredFlights && this.props.filteredFlights.length>0 ?
                                    <div className="font22 margin-r10 float-left">
                                        {this.props.filteredFlights.length} out of {this.props.flights.alt_inf.length} results
                                    </div>:
                                    <div className="font22 margin-r10 float-left">
                                        {this.props.flights.alt_inf.length} results
                                    </div>
                                }
                                <div className="cursor-pointer d-block d-lg-none float-left margin-t4">
                                    <FontAwesomeIcon icon={faFilter} onClick={()=>this.props.appActions.setMobFilters(!this.props.mobFilters)}/>
                                </div>
                            </Col>
                            <Col xs={12} lg={3} xl={3} className="padding0 d-none d-lg-block">
                                <Filters {...this.props} />
                            </Col>
                            <Col xs={12} lg={6} xl={7} className="padding0">
                                <Flights {...this.props} />
                            </Col>
                            <Col xs={12} lg={3} xl={2} className="bg-secondary text-white padding-tb50">
                                ADS PLACE
                            </Col>
                        </Row>:
                        <Image src="/images/st_logo_white.svg" width="70%" className="padding-tb50" />}
                    <Col className="clearfix"></Col>
                </Container>
            </div>
        );
    }
}

export default withRouter(ContentSearchFlights);