import React, { Component } from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faFacebookF,
//   faTwitter,
  faLinkedinIn,
//   faGooglePlusG,
//   faPinterestP,
  faInstagram
} from '@fortawesome/free-brands-svg-icons';
import {
  URL_FB,
//   URL_TW,
  URL_IN,
//   URL_GPLUS,
//   URL_PIN,
  URL_INSTAGRAM
} from '../common/const';

// social button links
class SocialButtons extends Component {
  render() {
    const social = [
      faFacebookF,
    //   faTwitter,
      faLinkedinIn,
    //   faGooglePlusG,
    //   faPinterestP,
      faInstagram
    ];
    // const socialCss = [
    //   'facebook',
    //   'twitter',
    //   'linkedin',
    //   'google',
    //   'pinterest',
    //   'instagram'
    // ];
    const links = [
        URL_FB, 
        // URL_TW, 
        URL_IN, 
        // URL_GPLUS, 
        // URL_PIN, 
        URL_INSTAGRAM
    ];
    return social.map((social, i) => (
        <a
            href={links[i]}
            key={i}
            // onClick={e => this.onClick(e, social)}
            className="text-white float-left margin-b10 margin-r10 padding5 font22"
            target="_blank"
            rel="noopener noreferrer"
        >
            <FontAwesomeIcon icon={social} />
        </a>
    ));
  }
}

export default SocialButtons;
