import moment from "moment"

//helper functions for validating user inputs
// if the input is valid, the input is valid, it will return an empty string as ""
// if there is a problem, it will return the error in a string, like "Your input is invalid"

export function validateNames(string) {
   if (string == "" || /^[\s]+$/gi.test(string)) return "This cannot be empty!"
   else if (/[^(A-Za-z\s)]+/gi.test(string)) return "Must contain only letters"
   else return ""
}

export function validateSelects(selection) {
   if (
      selection === "" ||
      selection === "--" ||
      selection === null ||
      selection === undefined ||
      selection === "0" ||
      /^\s*$/.test(selection) ||
      /^-/.test(selection)
   )
      return "empty!"
   else return ""
}

export function validateDates(bookForm, field, arr, inputCaptureProps) {
   const hasValue = arr.filter(item => {
      if (bookForm[field][item].value) {
         return item
      }
   })
   if (hasValue.length === 3) {
      let string = ""
      arr.forEach(item => {
         string += bookForm[field][item].value + "-"
      })
      string = string.slice(0, string.length - 1)
      const valid = moment(string, ["DD-MM-YYYY", "D-M-YYYY"], true).isValid()

      if (valid) {
         arr.forEach(item => inputCaptureProps(field, item, undefined, "", true))
         return false
      } else {
         arr.forEach(item =>
            inputCaptureProps(field, item, undefined, "Invalid date", true)
         )
         return true
      }
   }
}

export function validatePhone(phone) {
   return /^\d+$/.test(phone) ? "" : "Number is invalid!"
}

export function validateEmail(email) {
   return /^[\w-.+]+@[a-zA-Z0-9.-]+\.[a-zA-Z0-9]{2,4}$/.test(email)
      ? ""
      : "Email is invalid!"
}