import React, { Component } from 'react';
import Header from './Header';
import Routes from './Routes';
import { withRouter } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Card from 'react-bootstrap/Card';

class Page404 extends Component {

    render() {
        return (
            <div>
                <Header {...this.props} />
                <Routes {...this.props} />
                <div className="page-404-back info-pages-top">
                    <Container className="text-center">
                        <div className="font36 font-weight-bolder margin-b50">
                            It seems that we are lost!!!
                        </div>
                        <div className="font22">
                            We apologize, but we are unable to open the page you are looking for.
                        </div>
                    </Container>
                </div>
            </div>
        )
    }
}

export default withRouter(Page404);