import axios from 'axios';

export function paginate(array, page_size, page_number) {
    --page_number; 
    return array.slice(page_number * page_size, (page_number + 1) * page_size);
}

export function sort(key, order = 'asc') {
    return function(a, b) {
        let varA = '';
        let varB = '';

        varA = typeof a[key] === 'string' ? a[key].toUpperCase() : a[key];
        varB = typeof b[key] === 'string' ? b[key].toUpperCase() : b[key];
            
        let comparison = 0;

        if (varA > varB) {
            comparison = 1;
        } else if (varA < varB) {
            comparison = -1;
        }
        return order === 'desc' ? comparison * -1 : comparison;
    }
}

export function listHelper(n) {
    let temp = [];
    for (let i = 0; i < n; i++) temp[i] = i;
    return temp;
}

export function arrayNumbers(start, len, desc=false) {
    let temp = [];
    for (let i = 0; i < len; i++) temp[i] = desc?start-i:start+i;
    return temp;
}

export function objectCopy(src) {
    let target = {};
    for (let prop in src) {
      if (src.hasOwnProperty(prop)) {
        target[prop] = src[prop];
      }
    }
    return target;
  }


export function prepareAirlines(flights, filtersTemp) {
    let airlines = {};
    let airlinesTemp = [];
    let flightDetailsIn = [];
    let flightDetailsOut = [];
    axios
        .get('/data/airlines.json')
        .catch(err => {})
        .then(response => {
            for(let i=0;i<flights.length;i++) {
                if(!Array.isArray(flights[i].outbound.fli_inf)) {
                    flightDetailsOut[0] = flights[i].outbound.fli_inf;
                } else {
                    flightDetailsOut = flights[i].outbound.fli_inf;
                }
                if(flights[i].hasOwnProperty('inbound')) {
                    if(!Array.isArray(flights[i].inbound.fli_inf)) {
                        flightDetailsIn[0] = flights[i].inbound.fli_inf;
                    } else {
                        flightDetailsIn = flights[i].inbound.fli_inf;
                    }
                }
                for(let j=0;j<flightDetailsOut.length;j++) {
                    if(!airlinesTemp.includes(flightDetailsOut[j].airline_code)) {
                        airlinesTemp.push(flightDetailsOut[j].airline_code);
                    }
                }
                for(let j=0;j<flightDetailsIn.length;j++) {
                    if(!airlinesTemp.includes(flightDetailsIn[j].airline_code)) {
                        airlinesTemp.push(flightDetailsIn[j].airline_code);
                    }
                }
            }
            let temp = [];
            for(let i=0;i<airlinesTemp.length;i++) {
                temp = response.data.filter(function(e) {
                    return (e.code===airlinesTemp[i]);
                });
                if(temp) airlines[temp[0].code]=temp[0].name
                else airlines[airlinesTemp[i]]=airlinesTemp[i];
            }
            airlines['filter'] = airlinesTemp;
            if(filtersTemp!=='none') { 
                filtersTemp[6].airlines = airlinesTemp;
                return { airlines: airlinesTemp, filters: filtersTemp }
            }
            return { airlines: airlinesTemp }
        })
}